import React, { useContext, useEffect, useState } from 'react';
import {useNavigate} from 'react-router-dom'
import axios from 'axios';
import {useTranslation} from 'react-i18next'
import { MDBRow, MDBCol, MDBBtn, MDBInput, MDBAlert, MDBContainer, MDBView, MDBMask, MDBModal } from "mdbreact";
import './main.css'
import './upload.css'
import {Context}  from '../../Context';
import spinner from '../../images/spinner.svg'
import Loading from '../loading';
import { Button, Container, Form } from 'react-bootstrap';
import Ownership from '../../components/upload/ownership';
import Address from '../../components/upload/address';
import Facilities from '../../components/upload/facilities';
import Rules from '../../components/upload/rules';
import Images from '../../components/upload/image';
import Msg from '../../components/msg';
import Success from '../../components/upload/sucess';
import { handleNwErr } from '../../utils/handleNetworkErr';


export const setFormData=(state,user)=>{
  var formData = new FormData();
        formData.append('city',state.city)
        formData.append('landlord',state.landlord)
        formData.append('type',state.type)
        formData.append('description',state.description)
        formData.append('price',state.price)
        formData.append('rooms',state.rooms)
        formData.append('toilets',state.toilets)
        formData.append('guestnum',state.guestnum)
        formData.append('facilities',state.facilities)
         formData.append('rules',state.rules)
         formData.append('location',state.location)
         formData.append('livrooms',state.livrooms)
         formData.append('balconies',state.balconies)
        formData.append('discPrice',state.discPrice)
       formData.append('owner',state.owner)
       formData.append('id',parseInt(user.ui))
        formData.append('telephone',user.telephone)
        formData.append('mainImg',state.mainImg)
        
        //formData.append('date',new Date())
        if(state.id){
          formData.append('houseId',state.id)
        }
        else{
          formData.append('date',new Date())
        }
        for (const key of Object.keys(state.imgCollection)) {
            formData.append('imgCollection', state.imgCollection[key])
        }
        return formData
}
 export default function FilesUploadComponent () {
  
   const navigate = useNavigate()
   const {t}=useTranslation()
 const lastStep=7
     const {user,dispErr}= useContext(Context)
     const [regions,setRegions]=useState([])
     const [cities,setCities]=useState([])
     const [currentStep,setCurrentStep]=useState(1)
     const [loading,setLoading]=useState(false)
     const [msg,setMsg]=useState('')
     const [showMsg,setSM]=useState(false)
     const [err,setErr]=useState('')
    const next=(e)=>{
      e.preventDefault()
      if(currentStep<lastStep){
        setCurrentStep(currentStep+1)
      }
    }
    const prev=()=>{
      if(currentStep>1){
        setCurrentStep(currentStep-1)
      }
    }
    const handleChange=(e)=>{
      const {name,value}=e.target
         setState({ 
           ...state,
           [name]:value})
         }
    const [state, setState] = useState({
      landlord:'',
        imgCollection: [],
        price:null,
        discPrice:null,
        type:'',
        description:'',
        location:'',
        city:'',      
        rooms:null,
        toilets:null,
      
        owner:1,
        MAX_LENGTH:process.env.REACT_APP_IMG_NUM,            
      balconies:null ,
      livrooms:null,
      guestnum:null,
      mainImg:null,
      rules:[],
      facilities:[]

    })
      const setRules=(e)=>{
        let {value}=e.target
       
        setState({
          ...state,
          rules:state.rules.includes(value)?
            state.rules.filter((rule)=>{return rule!==value}):
           [...state.rules,value]
        })    
      }
      const setFacilities=(e)=>{
        let {value}=e.target
       console.log(value)
        setState({
          ...state,
          facilities:state.facilities.includes(value)?
            state.facilities.filter((facility)=>{return facility!==value}):
           [...state.facilities,value]
        })    
      }
        const setMainImg=(e)=>{
          let file=e.target.files[0];
          if(file.size/1024>5120){
           alert('each file should have a maximum size of 5 mega bytes')
           e.target.value=null
          }
          else{
           setState({
             ...state,
             mainImg:file
           })
          }
           
        }
        function setImgs(e) {
          //console.log(e.target.files[0].size);
            if (Array.from(e.target.files).length > state.MAX_LENGTH) {
                alert(`sorry. you Cannot upload files more than ${state.MAX_LENGTH}  images`);
                e.target.value=null
                return;
              }
            
              else {
                for(let i=0;i<e.target.files.length;i++){
                  if((e.target.files[i]).size/1024>5120){
                   alert('each file should have a maximum size of 5 mega bytes')
                   e.target.value=null
                  }
                  //console.log((e.target.files[i].size)/1024);
                }
              }
            
                setState({ 
                ...state,
                imgCollection: e.target.files 
              })
        }
    const save=(e)=>{
      e.preventDefault();
     
     
      setLoading(true)
      let formData=setFormData(state,user)
     // console.log(state)
      axios.post(process.env.REACT_APP_ADDRESS+"/house/new", formData, {
           
        withCredentials:true
      },{
        headers:{'Content-Type': 'multipart/form-data'}
      }).then(
        resp => {
        
       // setSM(true)
       // setMsg('success')
       setCurrentStep(currentStep+1)
        
        
        }).catch(err=>{
          console.log(err)
          handleNwErr(err,dispErr)
          if(err.response){
           console.log(err.response)
            if(err.response.data.auth){
                navigate('/login',{state:{msg:'session expired'}})
            }
            else {
              alert(err.response.data)
            }
        }
        else alert(err.message)
        })
        .finally(()=>{
          setLoading(false)
        })
    }

        return(
          <Container className='mh-100' style={{maxWidth:'100vw',overflowX:'scroll'}}>
          <p className='mt-2 text-center text-red'>{err}</p>
          <Loading show={loading}/>
            <div>
            <h2 className='text-center text-capitalize'>{t('upload')}</h2>
              <Ownership currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} state={state}/>
              <Address currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} state={state}/>
              <Facilities currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} setFacilities={setFacilities} state={state}/>
              <Rules currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} setRules={setRules} state={state}/>
              <Images currentStep={currentStep} prev={prev} save={save} handleChange={handleChange} lastStep={lastStep} handleAltImgs={setImgs} handleImg={setMainImg} state={state}/>
              {/*<Msg show={showMsg} hide={()=>navigate(-1)} msg={msg}/>*/}
              <Success currentStep={currentStep}/>
            </div>
          </Container>
        )
          
  
    
   
    
        
   
    
    
     
    
  
    
}
