import React, { useState } from 'react'
import { MDBRow, MDBCol, MDBIcon, MDBView, MDBCarouselItem, MDBCarouselInner, MDBCarousel, MDBCard  } from "mdbreact";
import {Card, Modal} from 'react-bootstrap'
import { useTranslation } from 'react-i18next';
import { FaCheck } from 'react-icons/fa';


export default function DetailsModal({house}) {
    const { t } = useTranslation();
    let images=house.other_images.trim().split(' ');
    const [facilities,setFacilities]=useState( house.facilities.trim(' ').split(','))
    const [rules,setRules]=useState(house.rules.trim().split(',')) 
  return (

    <Modal.Body>
    {images.length !=null && (
    <MDBCarousel
    activeItem={1}
    length={images.length}
    showControls={true}
    showIndicators={false}
    className="z-depth-1"
    slide
    >
    <MDBCarouselInner >
        {house.image!=null &&(
            <MDBCarouselItem itemId={images.length+1} >
        
            <MDBRow id=' ' style={{height:'55vh'}}>
            <img src={house.image} className=' ' alt='images' 
            style={{maxHeight:'100%',margin:'auto',width:'100%',height:'85vh', maxWidth:'100%'}}/>
            </MDBRow>
                                                       
        </MDBCarouselItem>
        )}
    {images.map(ims=>{
        return ( 
            
        <MDBCarouselItem itemId={images.indexOf(ims)+1} key={images.indexOf(ims)}>
        <MDBView>
            <MDBRow id=' ' style={{height:'55vh'}}>
            <img src={ims} className=' ' alt='images' 
            style={{maxHeight:'100%',margin:'auto',width:'100%',height:'85vh', maxWidth:'100%'}}/>
            </MDBRow>
        </MDBView>                                               
        </MDBCarouselItem>
    )
    })}                                            
    </MDBCarouselInner>                                                
    </MDBCarousel>
    
    )}
    {/*images.length<2 &&(
    <MDBCol md='8' className='  mx-auto'>
    <img src={images[0]} className=' w-100 d-block ' alt='images' style={{width:'100%',height:'40vh',margin:'auto'}} />
    </MDBCol>
    
    )*/}
    <br/>
   
    <MDBRow>
        <MDBCol md='6' xs='12' >
        <h3>Address</h3>
        <li className='text-capitalize'>City: {house.city}</li>                          
        <li className='text-capitalize'>location: {house.location}</li>
        </MDBCol>
        <MDBCol md='6' xs='12' >
        <h3 className='text-capitalize'>ID</h3>
        <li className='text-capitalize'>ref: {house.id} </li>
    
        <li className='text-capitalize'>Type: {house.type}</li>
        </MDBCol>
        
    </MDBRow>                     
<br/>
    <MDBRow>
        <MDBCol md='6' xs='12'>
        <h3>{t('uploads.details.properties').toUpperCase()}:</h3>
      
        <li> {t('choose.rooms')}: {house.rooms}</li>
    
        <li>{t('choose.toilets')}:{house.toilet} </li>
        <li>{t('choose.livrooms')}:{house.livrooms}</li>
         
        <li>
        {t('choose.guests')}:{house.guestnum}</li>
    
        </MDBCol>

        <MDBCol md='6' xs='12'>
        <h3>{t('uploads.details.description').toUpperCase()}:</h3>
        {(house.description !==null) && (
        <li>{house.description}</li>                          
    )}
        </MDBCol>
    </MDBRow>
    <MDBRow>
                      {facilities.length>0 && facilities[0].length>1 &&(
                        <MDBCol md={6} >
                       
                        <MDBCard>
                            <Card.Header className='fw-bold text-capitalize'>
                               {t('house dtails.facilities')}
                            </Card.Header>
                            <Card.Body>
                                {facilities.map((f,i)=>{
                                    return(
                                        
                                        <li key={i}>{t(f)}</li>
                                          
                                    )
                                })}
                               
                            </Card.Body>
                        </MDBCard>
                        
                        </MDBCol>
                        
                    )}
                    {rules.length>0 && rules[0].length>1 &&(
                            <MDBCol className='mt-2 mx-auto' >
                            <Card className='mx-auto'>
                                {/*<Card.Header className='fw-bold'>
                               {t('house dtails.not allowed')}
                                    
                                </Card.Header>*/}
                                <Card.Body>
                                    <ul>

                                        { rules.map((rule,i)=>{                                            
                                            return(
                                                <li key={i}>{t(rule)}{' '}<FaCheck/></li>
                                            )
                                        })}
                                    </ul>                                                                        
                                </Card.Body>
                            </Card>                    
                        </MDBCol>
                        )}       
                      </MDBRow>
    <br/>
    <MDBRow className='mx-auto'>    
        <MDBCol md='6' xs='12'>
        <h3>{t('uploads.details.price').toUpperCase()}:</h3>
        <li >{process.env.REACT_APP_CUR} {house.price}</li>
        </MDBCol> 
                                                     
    </MDBRow>
    
    <MDBRow>
    
    </MDBRow>
    </Modal.Body>
  )
}
