import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Col, ListGroup, Row } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import Loading from './loading';

export default function News() {
    const [news,setNews]=useState([])
    const [post,setPost]=useState()
    const [loading,setLoading]=useState(true)
    const {t}=useTranslation()
    
    useEffect(()=>{
        setLoading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/guests/news')
        .then(resp=>{
            console.log(resp.data);
            setNews(resp.data.articles)
            setPost(resp.data.post)
        })
        .catch(e=>{
            console.log(e);
        })
        .finally(()=>setLoading(false))
        },[])
  return (
    <div className='mh-100'>
    <Loading show={loading}/>
    <Row>
        <Col md={8} lg={7}  className='mx-auto'>
        {post!==undefined && (
                <>
                    <h3 className='text-center'>{post.title}</h3>
                    <p className='text-center'>{new Date(post.date).toLocaleDateString()}</p>
                    <div dangerouslySetInnerHTML={{__html: post.body}} className='mx-auto text-center'></div>
                </>
            )}
        </Col>
            
           
            <Col md={8} lg={4} className='mx-auto'>
            <h4 className='text-center text-capitalize'>{t('news.others')}</h4>
                {news.length>0 && news.map(n=>{
                    return(
                        <ListGroup>
                            <ListGroup.Item><Link to={'/news/'+n.slug}>{n.title}</Link></ListGroup.Item>
                        </ListGroup>
                    )
                })}
        </Col>
    </Row>
    
    </div>
  )
}
