import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Table,Button } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'
import Rating from 'react-rating'
import { FaRegStar, FaStar, FaTrashAlt } from 'react-icons/fa'
import { handleNwErr } from '../../utils/handleNetworkErr'
import { Context } from '../../Context'
import Loading from '../loading'



export default function Reviews() {
  const [reviews,setReviews]=useState([])
  const [loading,setLoading]=useState(false)
  const [rev2Drop,setRev2Drop]=useState()
  const [show,setShow]=useState(false)
  const {dispErr}=useContext(Context)
  const navigate=useNavigate()
  useEffect(()=>{
    setLoading(true)
    axios.get(process.env.REACT_APP_ADDRESS+'/a/reviews',{withCredentials:true})
        .then(resp=>{
            //console.log(resp.data);
            setReviews(resp.data)
        })
        .catch(e=>{
            console.log(e);
            handleNwErr(e,dispErr)
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
        })
        .finally(()=>setLoading(false))
  },[])
  const dropRev=(id)=>{   
    setLoading(true)
    axios.post(process.env.REACT_APP_ADDRESS+'/a/review/drop',{revId:id},{withCredentials:true})
        .then(resp=>{
            
            setReviews(reviews.filter(rev=>{
              return rev.id!==id
            }))
        })
        .catch(e=>{
            console.log(e);
            handleNwErr(e,dispErr)
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
        })
        .finally(()=>setLoading(false))

  }
  const publishRev=(id)=>{
    setLoading(true)
    axios.put(process.env.REACT_APP_ADDRESS+'/a/review',{revId:id},{withCredentials:true})
        .then(resp=>{
            
            setReviews(resp.data)
        })
        .catch(e=>{
            handleNwErr(e,dispErr)
            console.log(e);
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
        })
        .finally(()=>setLoading(false))
  }
  return (
    <div>
    
        <Table>
            <thead>
                <tr>
                    <th>Name</th>                    
                    <th>Email</th>
                    <th>Comment</th>
                    <th>rating</th>
                    <th>Status</th>
                </tr>
                </thead>
                <tbody>
                 {reviews.map((rev,i)=>{
                  return(
                    <tr key={i}>  
                    <td>{rev.fullname}</td>  
                    <td>{rev.email}</td>                
                    <td>{rev.message}</td>
                    <td><Rating
                        emptySymbol={<FaRegStar className="icon" />}
                        fullSymbol={<FaStar className="icon" />} 
                        initialRating={rev.rating}
                        readonly  
                      /></td>
                      <td>{
                        rev.visible===1 ?(
                          <Button color='pink'>Published</Button>
                        ):<Button onClick={()=>publishRev(rev.id)}>Publish</Button>
                      }</td>
                      <td><Button onClick={()=>dropRev(rev.id)}><FaTrashAlt /></Button></td>
                      </tr>
                  )
                 })}
                </tbody>
            
        </Table>
        <Loading show={loading}/>
    </div>
  )
}
