import React, { useEffect, useState } from 'react'
import { Button, Form,Row,Col, Modal, Card, Carousel } from 'react-bootstrap'
import { useTranslation } from 'react-i18next';

export default function ChangeImages({currentStep,prev,handleChange,lastStep,handleImg,handleAltImgs,save,state}) {
    const [showOld,setShowOld]=useState(false)
    const show=()=>setShowOld(true)
    const hide=()=>setShowOld(false)

    const {t}=useTranslation()
    useEffect(() => {
      console.log(state.images);
    
      
    }, [])
    
    if(currentStep!==5){
        return null;
    }
  return (
    <div className='card border-0 col col-md-8 mx-auto' id='images'>
            <div className='text-center'>
            <Button onClick={show}>see current Images</Button>
            </div>
        
        <ShowImages main={state.image} otherImgs={state.other_images} show={showOld} hide={hide}/>
        <Form onSubmit={save}>
        <h4 className='text-center'> {  } </h4>
        <Col md={8} className='mx-auto'>
        <Col>
            <Form.Label>{t('choose.mainImg')}</Form.Label>
            <Form.Control  type="file" accept='image/jpeg,image/png'  name='image' onChange={handleImg} ></Form.Control>
            <Button>Select New Image</Button>
        </Col>
        <Col>
            <Form.Label>{t('choose.imgs')}</Form.Label>
            <Form.Control  type="file"  accept='image/jpeg,image/png' multiple  name='imgCollection' onChange={handleAltImgs} ></Form.Control>
        </Col>
        
            <h4 className='text-center'>{''}</h4>
        <Col>
            <Form.Label>{t('choose.price')} ?</Form.Label>
            <Form.Control type='number' required name='price' onChange={handleChange} defaultValue={state.price}></Form.Control>
        </Col>
        <Col>
        <Form.Group>
            <Form.Label>{t('choose.disc_price')} ?</Form.Label>
            <Form.Control type='number' required name='discPrice' onChange={handleChange} defaultValue={state.discPrice}></Form.Control>
        </Form.Group>
        </Col>
        </Col>
       <div className='text-center'>
        <Button onClick={prev} disabled={currentStep<=1?true:false}>{t('choose.prev')}</Button>
              <Button type='submit'>{t('choose.submit')}</Button>
              </div>
        </Form>
    </div>
  )
}

export const ShowImages=({show,main,otherImgs,hide})=>{
    
    return(
        <Modal show={show} onHide={hide}>
            <Modal.Header closeButton/>
            <Modal.Body>
            <Card>
            <Carousel>
            {main && main.length>2 && (
                <Carousel.Item>
                    <img
                    className="d-block w-100"
                    src={main}
                    alt="main-img"
                    />
                    
            </Carousel.Item>            
            )}
            {otherImgs.trim().split(' ').map((img,index)=>{
                return(
                    <Carousel.Item key={index}>
                    <img
                    className="d-block w-100"
                    src={img}
                    alt="other-images"
                    />
                    
            </Carousel.Item>     
                )
            })}
            </Carousel>
            </Card>
           
            </Modal.Body>
        </Modal>
    )
}