import React from 'react'
import { Col, ListGroup } from 'react-bootstrap'
import { Link } from 'react-router-dom'

export default function Article({article,news,t}) {
    
  return (
    <div>
       
            <Col md={8} lg={6} className='mx-auto'>
            {typeof article !==undefined &&(
                <>
                <h3 className='text-center'>{article.title}</h3>
                <p className='text-center'>{new Date(article.date).toLocaleDateString()}</p>
                <div dangerouslySetInnerHTML={{__html: article.body}} className='mx-auto text-center'></div>
            </>

            )}
                
            </Col>
        <h4 className='text-center text-capitalize'>{t('news.others')}</h4>
        <Col md={8} lg={6} className='mx-auto'>
            {news.length>0 && news.map(n=>{
                return(
                    <ListGroup>
                        <ListGroup.Item><Link to={'/news/'+n.slug} target="_blank">{n.title}</Link></ListGroup.Item>
                    </ListGroup>
                )
            })}
        </Col>
    </div>
  )
}
