import React, { useEffect, useRef, useState } from 'react'
import { Alert, Col, Container, Form, FormControl, FormLabel, FormText, Modal, Row } from 'react-bootstrap'
import Rating from 'react-rating'
import {FaRegStar,FaStar} from 'react-icons/fa'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import Loading from './loading'

export default function NewReview() {
  useEffect(()=>{
    //console.log(location.state);
    setLoading(true)
    axios.get("https://restcountries.com/v2/all")
    .then(resp=>{
        let {data}=resp
        
       setCountries(data.map((country)=>{
            return country.name
        }))
    })
    .finally(()=>setLoading(false))
},[])
  const [rating,setRating]=useState(0)
  const [countries,setCountries]=useState([])
  const [loading,setLoading]=useState(false)
 
  const navigate=useNavigate()
  const {t}=useTranslation()
  const [review,setReview]= React.useState({
    name:'',
    email:'',
    message:'',
    country:''
  })
  const [msg,setMsg]=useState('')
  const [show,setShow]=useState(false)
  const editRating=(e)=>{
    setReview({
      ...review,
      [e.target.name]:e.target.value
    })
  }
  const submitReview=(e)=>{
    e.preventDefault();
    if(review.message.trim().length<1){
      setReview({
        ...review,
        message:''
      })
    }
    else if(review.name.trim().length<2){
      setReview({
        ...review,
        name:''
      })
    }
    else if (rating<1){
      setMsg(t('new review.rate'))
      setShow(true)
    }
    else{
      setLoading(true)
      axios.post(process.env.REACT_APP_ADDRESS+'/guests/review',{rating:rating,review})
      .then(resp=>{
        
        setMsg('thank you for your response')
        setShow(true)
        setReview({
          name:'',
        email:'',
        message:''})
        setRating(0)
        //navigate('/')
        alert('thank you for your review')
      })
      .catch(e=>{
        if(e.response){
          let status=e.response
          if(status===400){
            setMsg(t('new review.server err'));
          setShow(true)
          }
          else if(status===500){
            setMsg(t('new review.bad resp'));
          setShow(true)
          }
          else{
            setMsg(t('new review.bad email'))
            
            setShow(true)
          }
          console.log(e.response.status);
          
        }
        else console.log(e.config);
      })
      .finally(()=>setLoading(false))
    }
  }
  
  return (
   <Container fluid className='mh-100' id='new-review'>
      <h2 className='text-center'>{t('new review.heading')}</h2>
      {<Loading show={loading}/>}
      <Col className='card mx-auto text-capitalize' id='new-review-form' xs={12} md={8} lg={6}>
        <form onSubmit={submitReview}>
            <Row>
              <Col lg={6}>
                <FormLabel>{t('new review.name')}</FormLabel>
                <FormControl type='text' value={review.name} onChange={editRating} name='name' required/>
              </Col>
              <Col lg={6}>
                <FormLabel>Email</FormLabel>
                <FormControl type='email' value={review.email} onChange={editRating} name='email' required pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"/>
              </Col>
            </Row>
            <Row>
          
            </Row>
            <Row>
              <Col className='form-group mx-auto' lg={6}>
                <label>{t('reserveP.country')}</label>
                <Form.Select name="country" onChange={editRating} required>
                    <option value={''}></option>
                    {countries.map((c,i)=>{
                        return(
                            <option key={i} value={c}>{c}</option>
                        )
                    })}
                </Form.Select>
              </Col>            
              <Col lg={6}>
                <FormLabel >{t('new review.message')}</FormLabel>
                <FormControl as='textarea'  value={review.message} onChange={editRating} name='message' required title='message should not be empty'/>
              </Col>
            </Row>
            <Col lg={6} className='mt-3 mx-auto text-center'>
                <FormLabel className='me-2'>{t('new review.rating')}</FormLabel>
                <Rating
                  emptySymbol={<FaRegStar className="icon" />}
                  fullSymbol={<FaStar className="icon" />}
                  onClick={(val)=>{setRating(val)}}
              
                  />
              </Col>
            <button className='btn d-block mx-auto btn-lg' type='submit'>{t('new review.submit')}</button>
        </form>
      </Col>
      <Modal show={show} onHide={()=>setShow(false)}>
        <Modal.Header closeButton/>
        <Modal.Body>
          <p className='text-center'>{msg}</p>
        </Modal.Body>
      </Modal>
   </Container>
  )
}
