import { MDBBtn, MDBCard, MDBCardBody, MDBCardHeader, MDBCol, MDBContainer, MDBInput, MDBRow } from 'mdbreact'
import React, { useContext, useState } from 'react'
import axios from 'axios'
import { useNavigate } from 'react-router'
import Loading from '../loading'
import { useTranslation } from 'react-i18next'
import { handleNwErr } from '../../utils/handleNetworkErr'
import { Context } from '../../Context'

export default function GetEmail() {
    const [email,setEmail] = useState('')
    const [loading,setLoading]=useState(false)
    const navigate =useNavigate();
    const {dispErr}=useContext(Context)
    const {t}=useTranslation()
    const submit =(e)=>{
        e.preventDefault()
        setLoading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/user/email/'+email).then(
            res=>{
                
                navigate('/pm',{state:{email:res.data.email}})
            }
        ).catch(
            err=>{
                //console.log(err.response);
                if(err.response) alert(t('resetp.no email'))
                else {
                    handleNwErr(err,dispErr)
                }
            }
        ).finally(()=>setLoading(false))
    }
    
    return (
        <MDBContainer fluid style={{height:'89vh'}}>
        <Loading show={loading}/>
        <MDBRow style={{position:'relative',top:'10em'}} className='mx-auto'>
            <MDBCol xs='12' md='6' className='mx-auto'>
                <MDBCard className='border-0'>
                <h3 className='text-center black-text'>{t('resetp.title')}</h3>
                    <MDBCardBody>
                        <form onSubmit={submit}>
                            <MDBInput type='email' label={t('resetp.placeholder')} onChange={(e)=>{
                                setEmail(e.target.value)
                            }} required/>
                            <MDBBtn type='submit' >{t('choose.submit')}</MDBBtn>
                        </form>
                    </MDBCardBody>
                </MDBCard>
            </MDBCol>
        </MDBRow>
        </MDBContainer>
    )
}
