import axios from 'axios'
import { MDBBtn, MDBModalFooter, MDBTable, MDBTableBody, MDBTableHead } from 'mdbreact'
import React, { useContext, useEffect, useState } from 'react'
import { Button, Modal, Table } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom'
import { Context } from '../../Context'
import { handleNwErr } from '../../utils/handleNetworkErr'
import DetailsModal from '../houses/detailsModal' 
import Loading from '../loading'

export default function Reservations() {
    const [data,setData]=useState([])
    const [showModal,setShowModal]=useState(false)
    const [house,setHouse]=useState()
    const [showDropModal,setShowDropModal]=useState(false)
    const [reservationId,setReservationId]=useState()
    const [loading,setloading]=useState(false)
    const navigate=useNavigate()
    const {dispErr}=useContext(Context)
    useEffect(()=>{
        setloading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/a/bookings',{withCredentials:true})
        .then(resp=>{
            //console.log(resp.data);
            setData(resp.data)
        })
        .catch(e=>{
            console.log(e);
            handleNwErr(e,dispErr)
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
        })
        .finally(()=>setloading(false))
    },[])
    const getHouse=(id)=>{
        setloading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/a/'+id,
        {withCredentials:true, })
        .then(resp=>{
            setHouse(resp.data);
            setShowModal(true)
        })
        .catch(e=>{
            console.log(e);
            let response=e.response
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
                else if(response.status===404) alert('house had been deleted')
            }
        })
        .finally(()=>setloading(false))

    }
    const toggle=()=>{
        setShowModal(!showModal)
    }
    const changeStatus=()=>{
        //setReservationId()
        
        setShowDropModal(false);
        setloading(true)
        axios.put(process.env.REACT_APP_ADDRESS+'/a/bkstatus',{reserveId:reservationId},{withCredentials:true})
        .then(resp=>{
            //console.log(resp.data);
            setData(resp.data);
           // alert('done')
        })
        .catch(e=>{
            console.log(e);
            handleNwErr(e,dispErr)
            if(e.response){
                let response=e.response
                
                if(response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
                
            }
        })
        .finally(()=>{
            setloading(false)
            setReservationId()
        })
    }
  return (
    <div className='mh-100'>
    <Loading show={loading}/>
        <Table className='table'  maxHeight='40vh' >
           
           <thead>
               <tr>
                   <th>Name</th>
                   <th>Email</th>
                   <th>House</th>
                   <th>Date</th>
                   <th>checkIn</th>
                   <th>checkOut</th>
                   <th>Time</th>
                   <th>Telephone</th>
                   <th>Special Requests</th>
                   
               </tr>
           </thead>
           <tbody>
               {data.length>0 && data.map((r,index)=>{
                   return(
                       <tr key={data.id}>
                       <td>{r.fullname}</td>
                           <td>{r.email}</td>
                           <td><button onClick={()=>getHouse(r.houseId)}>{r.houseId}</button></td>
                           <td>{new Date(r.date).toDateString()}</td>
                           <td>{r.checkin}</td>
                           <td>{r.checkout}</td>
                           <td>{r.time}</td>
                           <td>{r.telephone}</td>
                           <td>{r.options}</td>
                           <td><Button onClick={()=>{
                               setReservationId(r.id)
                                setShowDropModal(true)
                           }}>Mark as Completed </Button></td>
                       </tr>
                   )
               })}
           </tbody>
           </Table>
           <Modal show={showModal} onHide={toggle}>
            <Modal.Header closeButton>
            <Modal.Title>House Details</Modal.Title>
            </Modal.Header>
            
            {house !==undefined && <DetailsModal house={house}/>}
            
            <Modal.Footer>
                <Button variant="secondary" onClick={toggle}>Close</Button>
            </Modal.Footer>
        </Modal>
         <Modal show={showDropModal} onHide={()=>setShowDropModal(false)}>
        <Modal.Header closeButton>
          {/*<Modal.Title>Modal heading</Modal.Title>*/}
        </Modal.Header>
        <Modal.Body><p className='text-center'>Are you sure you want to mark reservation with id {reservationId} as solved? </p></Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={changeStatus}>
            yes
          </Button>
          <Button variant="primary" onClick={()=>setShowDropModal(false)}>
           No
          </Button>
        </Modal.Footer>
      </Modal>
      <Loading show={loading}/>
    </div>
  )
}
