import React, { useContext, useEffect, useState } from "react";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBCard, MDBCardBody, MDBAlert } from 'mdbreact';
import axios from "axios";
import { Link, NavLink, useNavigate } from "react-router-dom";
import {regions,cr,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import {Context}  from "../../Context";
import AdminMenu from "./adminMenu";
import Loading from "../loading";
//import '../styles.css'
//const {}



 const NewAdmin = () => {
  const navigate =useNavigate();
  let {user,setUser}= useContext(Context)
  const [username,setUsername] = useState('')
  const [email,setEmail] = useState('')
  const [pw,setPw] = useState('')
  const [fullName,setFullName] = useState('')
  const [loading,setLoading]=useState(false)
  const [address,setAddress]= useState({
    region:'',
    city:'',
    address:''
  })
  const [tel,setTel] = useState(0)
  const [selected,setSelected] = useState('')
  useEffect(()=>{
    if(user.role!=='admin') navigate(-1);
},[])
  function createUser (e) {
    e.preventDefault();
    //console.log({address,fullname:fullName});
    setLoading(true)
    axios.post(process.env.REACT_APP_ADDRESS+'/admin/new',
    {username:username,
      email:email,
      password:pw,
      telephone:tel,
      region:address.region,
      city:address.city,
      address:address.address,
      fullname:fullName,
      id:parseInt(user.ui)
      
    },{withCredentials:true}).then(
      res =>{
        //console.log(res);
        alert(res.data)
       navigate('/admins/')
      }
    ).catch(e =>{
      if(e.response){
        if(e.response.data.auth){
            navigate('/login',{state:{msg:'session expired'}})
        }
    }
      })
      .finally(()=>setLoading(false))
     
    
  }
  function handleRegionSelect(e){
    setSelected(e.target.value)
    updateAddress(e)
   
  }
  function updateAddress(e){
      setAddress({
        ...address,
        [e.target.name]:e.target.value
      })
  }
  
  return (
    <div id='authPage'>
    <MDBContainer className='auth ' style={{ minHeight: '100vh',paddingTop: '5rem' }} fluid>
      <Loading show={loading}/>
      <MDBRow >
      <MDBCol  md='2' xs='10' sm='10'>

         <AdminMenu/>
        </MDBCol>
        <br/>
      <MDBCol sm='10' md='10' xs='10' style={{marginTop:'1.5em'}}>
      <MDBCol md="6" lg='6' className='mx-auto'>
          <MDBCard style={{opacity:'0.8'}} >
            <MDBCardBody>
              <form onSubmit={createUser}>
                <p className="h4 text-center py-4">New Admin</p>
               
                <div className="blue-text inl">
                <MDBRow>
                  <MDBCol xs='10' md='6' className='mx-auto'>
                  <MDBInput
                    label="Admin Full Name"
                    icon="user"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    value={fullName}
                    required
                    onChange={(e)=>{
                      setFullName(e.target.value)
                    }}
                  />
                  </MDBCol>
                  <MDBCol xs='10' md='6' className='mx-auto'>
                  <MDBInput
                    label="Admin Username"
                    icon="user"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    value={username}
                    required
                    onChange={(e)=>{
                      if([e.target.value].includes(' ')){
                        alert ('do not use the white space for username')
                      }
                      else setUsername(e.target.value)
                    }}
                  />
                  </MDBCol>
                </MDBRow>
                <MDBRow>
                  <MDBCol xs='10' md='6' className='mx-auto'>
                  <label htmlFor="region-select text-capitalize">Choose a Region</label>
                  <select onChange={handleRegionSelect} name='region' className="browser-default custom-select"  id='region-select' required>
                    <option ></option>
                    {regions.map(reg=>{
                        return (
                         <option className="text-capitalize" value={reg} key={reg} >{reg}</option>
                        )
                    })}
                    </select>
                  </MDBCol>
                  <MDBCol xs='10' md='6' className='mx-auto'>
                  <label htmlFor="city-select text-capitalize">Choose a City</label>
                    <select className="browser-default custom-select text-capitalize "  onChange={updateAddress}  id="city" name='city' required>
                    <option></option>
                    {
                  (selected==='Adamawa Region')? (
                     (
                      ar.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                     )

                  ): selected === 'Centre Region'? (
                    cr.map(c=>{
                         return (
                          <option value={c} key={c}>{c}</option>
                         )
                       })
                  ):selected=== 'East Region'?(
                    er.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ):selected=== 'Far North Region'?(
                    fnr.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ):selected=== 'Littoral Region'?(
                    lr.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ):selected=== 'North Region'?(
                    nr.map(c=>{
                         return (
                          <option value={c} key={c}>{c}</option>
                         )
                       })
                  ):
                  selected=== 'North West Region'?(
                    nwr.map(c=>{
                         return (
                          <option value={c} key={c}>{c}</option>
                         )
                       })
                  ):selected=== 'West Region'?(
                    wr.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ): selected=== 'South Region'?(
                    sr.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ):selected=== 'South West Region'?(
                    swr.map(c=>{
                         return (
                          <option value={c} key={c} >{c}</option>
                         )
                       })
                  ):
                  (<option></option>)

                  }
                  </select>
                  </MDBCol>
                </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>
                  <MDBInput
                      label="Type Your Address(e.g Rail Market,Bonaberi)"
                      icon="location-arrow"
                      group
                      type="text"
                      validate
                      error="wrong"
                      success="right"
                      value={address.address}
                      required
                      name='address'
                      onChange={updateAddress}
                    />
                 </MDBCol>
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>

                   
                    <MDBInput
                      label="Admin Email"
                      icon="envelope"
                      group
                      type="email"
                      validate
                      error="wrong"
                      success="right"
                      value={email}
                      required
                      onChange={(e)=>{
                        setEmail(e.target.value)
                      }}
                    />
                 </MDBCol>
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>
                  <MDBInput
                      label="Your Telephone"
                      icon="phone"
                      group
                      type="number"
                      validate
                      error="wrong"
                      success="right"
                      value={tel}
                      required
                      onChange={(e)=>{
                        setTel(e.target.value)
                      }}
                    />
                 </MDBCol>
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>
                  <MDBInput
                      label="Your Password"
                      icon="lock"
                      group
                      type="password"
                      validate
                      value={pw}
                      required
                      onChange={(e)=>{
                        setPw(e.target.value)
                      }}
                    />
                 </MDBCol>
               </MDBRow>
     
                </div>
                <MDBCol md="7" xs='9' className="mb-3">
            <div className="custom-control custom-checkbox pl-3">
              <input
                className="custom-control-input"
                type="checkbox"
                value=""
                id="invalidCheck"
                required
              />
              <label className="custom-control-label" htmlFor="invalidCheck">
               I Agree to <Link to='/terms'>Terms and Conditions</Link>
              </label>
              <div className="invalid-feedback">
                You must agree before submitting.
              </div>
            </div>
          </MDBCol>
                <div className="text-center py-4 mt-3">
                  <MDBBtn color="blue" type="submit">
                    Register
                  </MDBBtn>
                </div>
              </form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>  
      </MDBCol>
             
      </MDBRow>
  </MDBContainer>
  </div>
  );
};

export default NewAdmin;