import React, { useContext, useEffect, useState } from "react";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBCard, MDBCardBody, MDBModal } from 'mdbreact';
import axios from "axios";
import { Link, NavLink, useLocation, useNavigate } from "react-router-dom";
import {useTranslation} from 'react-i18next'
import CryptoJs from 'crypto-js'
import spinner from '../../images/spinner.svg'


import {Context} from "../../Context";
import Cookies, { set } from "js-cookie";
import Loading from "../loading";
import { Button, Modal } from "react-bootstrap";
import { handleNwErr } from "../../utils/handleNetworkErr";

//import AuthContext from '../../Context'

const LoginPage = () => {
  const navigate = useNavigate()
  const {t}= useTranslation()
  const [un,setUn] = useState('')
  const [pw,setPw] = useState('')
  const [msg,setMsg] = useState('')
  
  const [loading,setLoading]= useState(false)
  const [ptype,SetPtype] = useState('password')
  const [show,setShow]=useState(false)
   function ischecked(){
      if(ptype==='password') SetPtype('text')
      else SetPtype('password')
   }
  
  let {user,login,dispErr,showWarn} = useContext(Context)
  let location =useLocation()
  useEffect(()=>{
    if(location.state){
      alert(location.state.msg);
    }
   else if(user.isAuth===true && user.isAdmin===1) navigate('/admin/')
    else if(user.isAuth===true && user.role!==0) navigate('/user/')
    //console.log(process.env.REACT_APP_ADDRESS)
  },[])
  
 // let userData = {}
 // const [state,setState] = useState(false)
  //const secr8= '83At)9hj42'
  
  function loginC (e){
    e.preventDefault();
   ///console.log(un,pw);
   setLoading(true)
    axios.post('https://app.residencemg.co/client/user/login',{email:un,password:pw},{withCredentials:true}).then(
      (res)=>{ 
        console.log(res.data);
        /* login(res.data.userId,res.data.isAdmin)
         if(res.data.isProfileSet===false){
          showWarn()
         }*/
      
      }    
    ).catch(err=>
      {
        console.log(err.response);
        /*handleNwErr(err,dispErr)
        if(err.response)
        {
          if(err.response.status===403) setMsg('Account not activated. Please check your email to activate this account')
          else if(err.response.data) {
            setMsg(t('log_cred_err'))
            setShow(true)
            ;}
        }
        //else if(err.)
       
       
     //console.log(err);
        setUn('')
        setPw('')*/
      }
      ).finally(()=>{
        setLoading(false)
      })
  
 
//setCookie('user','sonia')
//setCookie('user1','samantha')
  }
  const styles ={
    marginTop:"5vh",
  
  }
return (
 
    <div id='authPage' style={{minHeight:'100vh',minWidth:'100vw'}}>
    <MDBContainer style={{ height: '100vh', width: '100%',paddingTop: '5rem' }} fluid className='auth'>
    <Loading show={loading}/>
      <MDBRow style={styles} styles={{marginTop:'10rem'}} className='text-capitalize'>
    
        <MDBCol md="9" lg="6"  className='mx-auto' sm='11' xs='11' >
        <MDBCard style={{opacity:'0.8'}}>
        <MDBCardBody>
          <form onSubmit={loginC} onClick={()=>setMsg('')}>
          <p style={{color:'red',textAlign:'center'}}>{msg}</p>
            <p className="h4 text-center mb-4">{t("log in")}</p>
            <p className=" text-center "><Link to='/signup'>{t("lg.na")}</Link></p>
            <div className="grey-text">
              <MDBInput label="E-mail"  group type="email" validate value={un} error="wrong" icon='mail'
                success="right" onChange={(e)=>{
                  if(e.target.value.charAt(0)===' ') setUn('')
                  else setUn(e.target.value)
                }} name='email' required />
                
              <MDBInput label={t("passw")} icon="lock" value={pw} group type={ptype} validate name='password' onChange={(e)=>{
                 setPw(e.target.value)
              }} required />
              <div className="custom-control custom-checkbox">
                <input type="checkbox" className="custom-control-input" id="check" onChange={ischecked}/>
                <label className ="custom-control-label" htmlFor="check">{t('showpass')}</label>
            </div>
            </div>
           
            <div className="text-center">
            <p><Link to='/getemail'>{t('lg.fp')}</Link></p>
              <MDBBtn type='submit' color='blue'>{t("log in")}</MDBBtn>
              
            </div>
          </form>
          </MDBCardBody>
          </MDBCard>
        </MDBCol>
        
      </MDBRow>
    </MDBContainer>
    <Modal show={show} onHide={()=>setShow(false)}>
      <Modal.Header closeButton/>
      <Modal.Body>
        <p className="text-center">{msg}</p>
      </Modal.Body>
      <Button onClick={()=>setShow(false)}>{t('close')}</Button>
    </Modal>
    </div>
  
);
};

export default LoginPage;