import React from 'react'
import { Button, Form,Row,Col } from 'react-bootstrap'
import { useTranslation } from 'react-i18next';


export default function Rules({currentStep,next,prev,handleChange,lastStep,setRules,state}) {
    const rules=[
        'smoking', 
        'pets',
       'children', 
        'events',
        'caution deposit',
        ]
       const {t} =useTranslation()
    if(currentStep!==4){
        return null;
    }
  return (
    <div className='px-2 py-4 mx-md-auto col-11 col-md-8 col-lg-7'>
        <Form onSubmit={next}>
        <h4 className='text-center'>{t("choose.select rules")}</h4>
            <Row >
                {rules.map((rule,i)=>{
                    return(
                        <Col key={i} md={6} >
                        <Form.Check 
                            
                            type='checkbox'
                            name={rule}
                            label={t('choose.rules.'+rule)}
                            value={rule}
                            onChange={setRules}
                            defaultChecked={state.rules.includes(rule)}
                        />
                        </Col>
                    )
                })}
                </Row>
                <h5 className='text-center'>{t('choose.desc_title')}</h5>
                <Form.Group className='col-12 col-md-8 col-lg-8 '>
                
                    <Form.Label>{t("choose.description")}</Form.Label>
                    <Form.Control as='textarea' name='description' onChange={handleChange} defaultValue={state.description}/>
                </Form.Group>
                <div className='text-center'>
                <Button onClick={prev} disabled={currentStep<=1?true:false}>{t("choose.prev")}</Button>
              <Button  disabled={currentStep>=lastStep?true:false} type='submit'>{t("choose.next")}</Button>
                </div>
               
        </Form>
    </div>
  )
}
