import { MDBBtn,  MDBCardBody, MDBCardImage, MDBCol } from 'mdbreact'
import React from 'react'
import { Button, Card, Col, Row } from 'react-bootstrap';
import { createSearchParams, useNavigate } from 'react-router-dom';

export default function House({h,t}) {
    const navigate=useNavigate()
    const params = { id: h.id };
    //let images= h.images.imageUrl.trimRight().split(' ');
        let mainImg=h.image;
  return (
    <MDBCol key={h.id} md='4'   xs='10'>
          <Card style={{height:'90%'}} className='mx-auto  text-white' onClick={
            ()=>{
              navigate({
                pathname:'/house/details',

                search:createSearchParams({id:h.id}).toString()
              })
              }}>
            
             
                <Card.Img src={mainImg} alt='image of house prop' className='search-card-disp ' />
             
             <Card.ImgOverlay className='d-flex align-items-end text-center'>
             <Card.Text className='overlay-text fw-bold w-100'> {h.city}| {t('htype.'+h.type.toLowerCase())}| {h.price} {' '}{process.env.REACT_APP_CUR}
             </Card.Text>
             
             </Card.ImgOverlay>
              <MDBCardBody>
                
                  
                 {/*<DetailsModal                  
                 images={images}
                 h={h}
                 modalToggle={modalToggle}
                 closeModal={closeModal}
               //  t={t}
                 data={data}
                  />*/}
                  </MDBCardBody>
             
          </Card>
      </MDBCol>
  )
}


