import React from 'react'
import { Button } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'

export default function SignupPartOne({action}) {
    const {t}=useTranslation()
  return (
    <div className='mh-100'>
            <div id='signup-part-one'>
            <div className=' col-10 col-md-8 col-lg-5 mx-auto text-center'>
            <h2 >{t('signup.title')} </h2>
                <p className='text-justify'>{t('signup.p1')}</p>
            <Button onClick={action} className='btn-purple text-light'>{t('signup.get_started')}</Button>
            </div>
                
        </div>
    </div>
  )
}
