import axios from 'axios'
import React from 'react'

export default function RPass() {
    const email=0
    function submit(){
        axios.post()
    }
    return (
        <div  className='container' style={{height:'80vh',position:'relative',top:'8em',left:"3em"}}>
            <div className="col-sm-8" id="col" >
    <div className="card" id="rspw">
        <h2 className="text-center">Reset Your password for </h2>
        <div className="card-body">
            <form className="form-signin mx-auto" method="POST" action="/resetP">
                <div className="col-sm-4">
                       <label>Email</label>
                    <div className="form-group "> 
                        <input type="email" class="form-control"  name="email" readonly value='<%= email%>'/> 
                    </div> 
                       <label>New Password</label>
                    <div className="form-group pass_show"> 
                        <input type="password"  class="form-control" placeholder="Confirm Password" name="psw" required min="8"/> 
                    </div> 
                    <button type="submit" >Save new Password</button>
                </div>  
              </form>
        </div>
       </div>
</div>    
        </div>
    )
}
