import React, { Component } from 'react';
import { MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavItem, MDBNavLink, MDBCollapse, MDBContainer,
MDBHamburgerToggler, 
MDBNav} from 'mdbreact';
import {withTranslation} from 'react-i18next'
import { Link } from 'react-router-dom';

class AdminMenu extends Component {
state = {
  collapse1: false,
  collapseID: ''
}

toggleCollapse = collapseID => () => {
  this.setState(prevState => ({ collapseID: (prevState.collapseID !== collapseID ? collapseID : '') }));
}

toggleSingleCollapse = collapseId => {
  this.setState({
    ...this.state,
    [collapseId]: !this.state[collapseId]
  });
}

render() {
  const {t} = this.props;
  return (
   
      <MDBContainer fluid>
        <MDBNav color="amber lighten-4" className='flex-column font-weight-bold'  style={{height:'40vh'}}>
          <MDBContainer>
            <MDBNavbarBrand>
              Admin Menu
            </MDBNavbarBrand>
           {/* <MDBHamburgerToggler color="blue" id="hamburger1" onClick={()=> this.toggleSingleCollapse('collapse1')} />
              <MDBCollapse isOpen={this.state.collapse1} navbar>*/}
                <MDBNavbarNav >
                  <MDBNavItem active>
                    <Link to="/admin/">{t('admin.hs')}</Link>
                  </MDBNavItem>
                  <MDBNavItem>
                    <Link to="/admin/all-users">{t('admin.urs')}</Link>
                  </MDBNavItem>
                  <MDBNavItem>                  
                    <Link to="/admin/all-admins">{t('admin.adms')}</Link>
                  </MDBNavItem>
                  <MDBNavItem>                  
                    <Link to="/admin/new-admin">{t('admin.add')}</Link>
                  </MDBNavItem>
                  <MDBNavItem>                  
                    <Link to="/admin/bookings">Bookings</Link>
                  </MDBNavItem>
                </MDBNavbarNav>
            {/**</mdbcollapse */}
          </MDBContainer>
        </MDBNav>
      </MDBContainer>
   
    );
  }
}

export default withTranslation()(AdminMenu);
