import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Button, Form } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'


export default function Ownership({currentStep,next,prev,handleChange,lastStep,state}) {
    const [types,setTypes]=useState([])
    const [loading,setLoading]=useState(false)
  const {t}=useTranslation()
    useEffect(()=>{
      console.log(state.owner);
       setLoading(true) 
       axios.get(process.env.REACT_APP_ADDRESS+'/house/aptmt')
       .then(resp=>{
        setTypes(resp.data)
       },[])
       .catch(e=>{
        console.log(e);
        //handle here
       })
    },[])
    if(currentStep!==1){
        return null
    }
  return (
    <div id='upload-ownership'>
       <Form onSubmit={next}>
        <h4 className='text-center'>{t('choose.warn')}</h4>
        <Form.Group>
        <Form.Check
            
            label={t("choose.i own")}
            name="owner"
            type='radio'
            value={1}
            required
            onChange={handleChange}
            
            defaultChecked={ state.owner===1}
            

            
          />
          <Form.Check
            
            label={t("choose.permitted")}
            name="owner"
            type='radio'
            value={0}
            required
            onChange={handleChange}
            defaultChecked={ state.owner===0}
            
          />
        </Form.Group>
        
        <Form.Group>
        <Form.Label className='fw-bold'>{t("choose.type")}</Form.Label>
        <Form.Select aria-label=" select type" required  onChange={handleChange} name='type' value={state.type}>
        <option value=''>{t("choose.choose")}</option>
            {types.length>0 && types.map(type=>{
                return(
                    <option key={types.indexOf(type)} value={type}>{t("htype."+type.toLowerCase())}</option>
                )
            })}
            </Form.Select>
        </Form.Group>
       
          <Form.Group>
            <Form.Label>{t('choose.ln')}</Form.Label>
            <Form.Control type='tel' required name='landlord' onChange={handleChange} value={state.landlord}/>
          </Form.Group>
        
        <div className='text-center'>
        <Button onClick={prev} disabled={currentStep<=1?true:false}>{t("choose.prev")}</Button>
              <Button  disabled={currentStep>=lastStep?true:false} type='submit'>{t("choose.next")}</Button>
        </div>
        
        </Form>
    </div>
  )
}
