import React, { Component, useState } from 'react'
import { instanceOf } from 'prop-types'
import  CryptoJs from 'crypto-js'
//import {withCookies,Cookies} from 'react-cookie'
import Cookies from 'js-cookie'
import { useNavigate} from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import axios from 'axios'

const Context = React.createContext()

 function AuthProvider({children}){
   //secr8= '83Atre khd)$%^&5Fhj42'
  const {t}=useTranslation()
  const navigate=useNavigate();
    let exTime=new Date(Date.now()+ (2 * 3600000))
      const [state,setState] = useState({ 
        user:{
          isAuth: ( Cookies.get('rmg_iu'))?true:false,
           
            ui:parseInt(Cookies.get('rmg_iu')),
            isAdmin:parseInt(Cookies.get('rmg_ia')),
            
        }
        
        
       })
      const [showErr,setShowErr]=useState(false)
      const [warn,setwarn]=useState(false)
        
    const session_exp=()=>{
      navigate('/login',{state:{msg:t('session_exp')}})
    }
     
     const login=(ui,isAdmin) =>{      
        Cookies.set('rmg_iu',ui,{expires:exTime})
        Cookies.set('rmg_ia',isAdmin,{expires:exTime})       
        setState({
          user:
          { isAuth: true ,
           
            ui:ui,
            isAdmin:isAdmin,
          

          }})
        if(isAdmin===1){
          
        navigate('/admin/')
        }
        else{
          navigate('/user/')
        }
         
      }
  
     const logout=()=> {
       axios.get(process.env.REACT_APP_ADDRESS+'/user/logout',{withCredentials:true})
        Cookies.remove('rmg_iu')
        Cookies.remove('rmg_ia')
        
        setState({
          user:
          { isAuth: false ,
            
            ui:'',
            isAdmin:'',
            

          }})
          navigate('/');
      }

      const dispErr=()=>{
        setShowErr(true)
      }
      const hideErr=()=>setShowErr(false)
    const showWarn=() =>setwarn(true)
    const hideWarn=()=>setwarn(false)
        return (
            <Context.Provider
        value={{
          user:state.user,
          login:login,
          logout:logout,
          session_exp:session_exp,
          dispErr:dispErr,
          showErr:showErr,
          hideErr:hideErr,
          showWarn:showWarn,
          hideWarn:hideWarn,
          warn:warn
        
        }}
      >
        {children}
      </Context.Provider>
        )
    
}
const AuthConsumer = Context.Consumer


export { AuthProvider, AuthConsumer,Context }


