import React, { useContext, useEffect, useState } from 'react'
import {I18nextProvider} from 'react-i18next'
import i18n  from './i18n'
import "mdbreact/dist/css/mdb.css"
import '@fortawesome/fontawesome-free/css/all.min.css';
import "bootstrap-css-only/css/bootstrap.min.css";

import LoginPage from './pages/authenticate/login'
import {Routes,Route, useNavigate} from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css'
import Default from './pages/default';
import Signup from './pages/authenticate/signup';
import Navigation from './components/navigation';
import HomePage from './pages/homepage'


//import SearchHouse from './components/search/searchHouse'
import {CookiesProvider} from 'react-cookie'

import Contact from './pages/Contact'
import AboutPage from './pages/About';
import Terms from './pages/terms';
import Footer from './components/footer';
 





import {AuthProvider} from './Context'

import GetEmail from './pages/authenticate/getEmail';

import { AccConfirm, Messg4EmailConf, Messg4PassConf, PasswChangeConfirm } from './pages/authenticate/ca';
import RPass from './pages/authenticate/newPass';
import GuestHouse from './pages/search/GuestHouse';

import './pages/style.css'
import MakeReservation from './pages/search/makeReservation';

import AdminIndex from './pages/Admin';
import UserIndex from './pages/user';
import Search from './pages/search/search';
import NewReview from './pages/newReview';
import HouseDetails from './pages/search/houseDetails';
import News from './pages/news';
import Reviews from './pages/reviews';
import NewsArticle from './pages/newsArticle';

import ErrorAlert from './components/errorAlert';
import SetProfileAlert from './components/setProfileAlert';




function App() {
  const [isOffline,setIsOffline]=useState(false)
  
  useEffect(()=>{
    if(!navigator.onLine){
      setIsOffline(true)
    }
  },[])
 
  return (
    <AuthProvider>
   
      <I18nextProvider i18n={i18n}>
      <CookiesProvider>
        
          
          <React.Fragment>
            <Navigation/>
            <SetProfileAlert/>
            
              <Routes>
                <Route exact path='/' element={<HomePage/>}/>
                <Route path='/login' element={<LoginPage/>}/>
                <Route path='/signup' element={<Signup/>}/>
               
                {/*<Route path='/search' element={<Default/>}/>*/}
                <Route path='house/details' element={<HouseDetails/>}/>
                <Route path='/contact' element={<Contact/>}/>
                <Route path='/about' element={<AboutPage/>}/>
                <Route path='/terms' element={<Terms/>}/>
                <Route  path='/search' element={<Search/>}/>
                <Route  path='/guest-houses' element={<GuestHouse/>}/>
                <Route  path='/book' element={<MakeReservation/>}/>
                <Route path='/new-review' element={<NewReview/>}/>
                <Route path='/admin/*' element={<AdminIndex/>}/>
                <Route path='/news' element={<News/>}/>
                <Route path='/news/*' element={<NewsArticle/>}/>
                <Route path='/reviews' element={<Reviews/>}/>
                <Route path='/user/*' element={<UserIndex/>}/>
               
                <Route  path='/getemail' element={<GetEmail/>}/>
                <Route  path='/ac' element={<AccConfirm/>}/>
                <Route  path='/pc' element={<PasswChangeConfirm/>}/>
                <Route  path='/em' element={<Messg4EmailConf/>}/>
                <Route  path='/pm' element={<Messg4PassConf/>}/>
                <Route  path='/rp' element={<RPass/>}/>
                
                <Route path='*' element={<Default/>}/>
              
            </Routes>
            <ErrorAlert/>
            <Footer/> 
          </React.Fragment>
      
      </CookiesProvider>
      </I18nextProvider>
   
     
    </AuthProvider>
  );
}

export default App;
