import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Button, Card, Col, Row, Table } from 'react-bootstrap'
import { Link, useNavigate } from 'react-router-dom'
import { Context } from '../../Context'
import { handleNwErr } from '../../utils/handleNetworkErr'
import Loading from '../loading'

export default function AllPosts() {
    const navigate=useNavigate()
    //make it a table of posts instead
    const [posts,setPosts]=useState([])
    const {dispErr}=useContext(Context)
    const [loading,setLoading]=useState(false)
    useEffect(()=>{
        //console.log('hi')
        setLoading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/a/posts',{withCredentials:true}).then(resp=>{
           // console.log(resp.data);
           setPosts(resp.data);
        })
        .catch(e=>{
            
            console.log(e);
            handleNwErr(e,dispErr)
        })
        .finally(()=>setLoading(false))
    },[])
    const goTo=(post)=>{
       navigate('/admin/post',{state:post})
       
    }
  return (
    <div>
    <Loading show={loading}/>
        <Link to='/admin/new-post' className='btn btn-lg'>New Post</Link>
        <h3 className='text-center'>All posts</h3>
        <Table>
        <thead>
            <tr>
                <th>Title</th>            
                <th>status</th>
                <th>Date</th>
            </tr>
        </thead>
        <tbody>
            {posts.length>0 && posts.map(post=>{
                    return(
                        <tr key={post.id} className='mx-auto' onClick={()=>goTo(post)}>
                            <td>{post.title}</td>
                            <td>{post.status===1?"published":'draft'}</td>  
                            <td>{new Date(post.date).toLocaleDateString()}</td>
                           
                        </tr>
                    )
                })}
        </tbody>
           
        </Table>
    </div>
  )
}
