import React, { useContext, useEffect, useState } from 'react'
import GuestHouse from './GuestHouse'
import { useLocation, useNavigate,useParams, useSearchParams } from 'react-router-dom';
import HousesDisp from '../../components/houseDisplay';
import axios from 'axios';
import Default from '../default';
import Loading from '../loading';
import { useTranslation } from 'react-i18next';
import { Context } from '../../Context';
import { handleNwErr } from '../../utils/handleNetworkErr';
export default function Search() {
    const {t}=useTranslation()
   let params=useLocation()
   const navigate=useNavigate()
   const [houses,setHouses]=useState([])
   const [city,setCiy]=useState('')
   const [loading,setLoading]=useState(false)
   const [p,setp]=useSearchParams()
   const {dispErr}=useContext(Context)
    useEffect(()=>{
        let search=p.get('city')
        if(!search){
            navigate('/guest-houses')
        }
        else{
            console.log(params.search);
            
            //console.log('city encoded: '+decodeURIComponent(search));
            //console.log();
            setCiy(search)
            setLoading(true)
            
            axios.post(process.env.REACT_APP_ADDRESS+'/guests/search',{city:search}).then(
                (resp)=>{
                // console.log(resp.data);
                    if(Array.isArray(resp.data)){
                        setHouses(resp.data)
                    
                    }
                
                }
            ).catch(
                err=>{
                    console.log(err)
                    handleNwErr(err,dispErr)
                }
            )
         .finally(
                ()=>setLoading(false)
            )
            }
        
    },[])
    
  return (
   <div className='container-fluid mh-100'>
   <h2 className='text-center text-capitalize'>{t('search-result')} {city}</h2>
   <Loading show={loading}/>
   {loading===false && houses.length>0 ? (
    <HousesDisp houses={houses} hpp={6}/>
   ):(
    <p className='text-center'>{t('no_search_result')}</p>
   )}
    
    
   </div>
  )
}
