import { MDBCol, MDBContainer, MDBListGroup, MDBListGroupItem, MDBRow } from 'mdbreact'
import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import {withTranslation} from 'react-i18next'

 class Terms extends Component {
    render() {
        let styles = {
            fontWeight:'normal'
        }
        const {t}=this.props;
        return (
            <MDBContainer fluid>
            <h2 className='w-responsive  text-center mx-auto  mt-3 '>{t('t&c.heading')}</h2>
            <MDBRow style={{marginTop:'2rem'}}>
            <MDBCol  md='3' className='mx-auto' xs='10'  style={{maxWidth:'80%'}}>
              
                    <MDBContainer >
                        <MDBListGroup style={{ maxWidth:'80% !important' }}>
                            <MDBListGroupItem> <a href="#requirements">{t('t&c.sec1.subh')}</a></MDBListGroupItem>
                            <MDBListGroupItem><a href="#pricing">{t('t&c.sec2.subh')}</a></MDBListGroupItem>
                            <MDBListGroupItem> <a href="#benefits">{t('t&c.sec3.subh')}</a></MDBListGroupItem>
                            <MDBListGroupItem> 
                                        <a href="#dispute">{t('t&c.sec4.subh')}</a>
                             </MDBListGroupItem>
                            <MDBListGroupItem><a href="#dispute">{t('t&c.sec5.subh')}</a></MDBListGroupItem>
                            
                        </MDBListGroup>
                    </MDBContainer>

      
                   
                
            </MDBCol>
            <MDBCol  md='8' xs='10' className='text-justify'>
            <div >
                <MDBContainer >
                    {/*<!--h4 className="lead grey-text w-responsive text-center mx-auto mb-5 mt-3 text-uppercase font-weight-bold">welcome to  RESIDENCE M.G</!--h4-->*/}
                    <MDBRow>
                        <p >
                        {t('t&c.p1')}
                         </p>
                         <p >
                        {t('t&c.p2')}
                        
                         </p>
                         <p style={{fontWeight:'normal'}}>
                        {t('t&c.p3')}
                         </p>
                    </MDBRow>
                    <MDBRow>
                        <h4 id='requirements' className='title'>{t('t&c.sec1.subh')}</h4>
                         <ol className='mx-4'>
                            <li>{t('t&c.sec1.li.li1')}</li>
                            <li>{t('t&c.sec1.li.li2')}</li>
                            <li>{t('t&c.sec1.li.li3')}</li>
                            <li>{t('t&c.sec1.li.li4')}</li>
                            <li>{t('t&c.sec1.li.li5')}</li>
                            <li>{t('t&c.sec1.li.li6')}</li>
                            <li>{t('t&c.sec1.li.li7')}</li>
                            <li>{t('t&c.sec1.li.li8')}</li>
                         </ol>
                    </MDBRow>
                    <MDBRow>
                        <h4 className='title' id='pricing'>{t('t&c.sec2.subh')}</h4>
                        <p >{t('t&c.sec2.p1')}</p>
                        <p >{t('t&c.sec2.p2')}</p>
                    </MDBRow>
                    <MDBRow>
                        <h4 className='title' id='benefits'>{t('t&c.sec3.subh')}</h4>
                        <p >{t('t&c.sec3.p1')}</p>
                    </MDBRow>
                    <MDBRow>
                        <h4 className='title' id='added-value'>{t('t&c.sec4.subh')}</h4>
                        <p style={styles}>{t('t&c.sec4.p1')}</p>
                        <ol className='mx-4'>
                            <li>{t('t&c.sec4.li.li1')}</li>
                            <li>{t('t&c.sec4.li.li2')}</li>
                            <li>{t('t&c.sec4.li.li3')}</li>
                            <li>{t('t&c.sec4.li.li4')}</li>
                        </ol>
                    </MDBRow>
                    <MDBRow>
                        <h4 className='title' id='dispute'>{t('t&c.sec5.subh')}</h4>
                        <p style={styles}>{t('t&c.sec5.p1')}</p>
                    </MDBRow>
                    
                    {/*<p>This document was last updated on January 28, 2021</p>*/}
                
                </MDBContainer>
                <address>
                    <p>Email:social@residencemg.co</p>
                    <p>Contact: +237 651 705 406</p>
                </address>
                </div>
            </MDBCol>
            </MDBRow>
            </MDBContainer>
        )
    }
}

export default withTranslation() (Terms)