import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { Card, Col, Row } from 'react-bootstrap'
import Loading from './loading'
import Rating from 'react-rating'
import {FaRegStar,FaStar} from 'react-icons/fa'
import { useTranslation } from 'react-i18next'

export default function Reviews() {
    const [reviews,setReviews]=useState([])
    const [loading,setLoading]=useState(false)
    const {t}=useTranslation()
    useEffect(()=>{
        setLoading(true)
    axios.get(process.env.REACT_APP_ADDRESS+'/guests/reviews')
    .then(resp=>{
        console.log(resp.data);
        setReviews(resp.data)
    })
    .catch(e=>{
        console.log(e);
    })
    .finally(()=>setLoading(false))
    },[])

  return (
    <div className='mh-100'>
    <Loading show={loading}/>
        <h2 className='text-center'>{t('new review.reviews')}</h2>
        <Row>
            {reviews.map(review=>{
                return(
                    <Col className=' my-2 mx-md-auto' sm={8} md={5} lg={5}>
                        <Card>
                        <Card.Text className='text-md-center'>{review.fullname}<span> ~ </span>{review.country}</Card.Text>
                        <Rating
                            emptySymbol={<FaRegStar className="icon" size={25} />}
                            fullSymbol={<FaStar className="icon" size={25}/>}
                            initialRating={review.rating}
                            readonly={true}
                                className='text-center'
                            />
                        <Card.Text className='text-center'>{review.message}</Card.Text>
                        </Card>
                    </Col>
                )
            })}
        </Row>
    </div>
  )
}
