import React, { useContext, useEffect, useState } from 'react'
import {  MDBRow, MDBCol, MDBBtn } from 'mdbreact';
import axios from 'axios';
import {Context}  from '../../Context';
import { useNavigate } from 'react-router';
import {useTranslation} from 'react-i18next'
import Loading from '../loading';
import { handleNwErr } from '../../utils/handleNetworkErr';

export default function UpdatePassword() {
    const navigate = useNavigate()
    const {t}= useTranslation()
    const[type,setType]= useState('text')
    const[userPass,setUserPass]=useState({
        oldPass:'',
        newPass:''
    })
    const [loading,setLoading]=useState(false)
    let {user,session_exp,dispErr}= useContext(Context)
    
    useEffect(()=>{

        if(!user.isAuth){
            navigate('/login')
        }  },[])
    function updateUserPass(e){
        setUserPass({
            ...userPass,
            [e.target.id]:e.target.value
        })
    }
    function handleSubmit(e){
        e.preventDefault()
        //console.log(userPass);
        setLoading(true)
        axios.put(process.env.REACT_APP_ADDRESS+'/user/psw',{
            oldPass:userPass.oldPass,
            newPass:userPass.newPass,
            id:parseInt(user.ui)
        },{withCredentials:true}).then(
            res=>
                {           
                alert(res.data) 
                if(user.isAdmin===1)navigate('/admin/settings')
                else navigate('/user/settings')
            }
        ).catch(
            err=>{
                console.log(err);
                handleNwErr(err,dispErr)
                if(err.response){
                    if(err.response.data.auth){
                        //navigate('/login',{state:{msg:t('session_exp')}})
                        session_exp()
                    }
                    else if (err.response.data)alert(t('settings.pass_err'));
                }
            }
        )
        .finally(()=>setLoading(false))
    }
    return (
        <div className='container mh-100'>
        <Loading show={loading}/>
            <MDBRow >
    <MDBCol xs="12" sm='11' md='8' lg='6' className='text-capitalize mt-2 mx-auto text-dark'>
      <form onSubmit={handleSubmit} style={{backgroundColor: "#bb8fce", }} className='px-2' >
        <h4 className="mt-2 text-center mb-4 white-dar">{t('settings.change_password')}</h4>
        <label htmlFor="oldPass" className="">
          {t('oldp')}
        </label>
        <input type='password' id="oldPass" className="form-control" onChange={updateUserPass}/>
        <br />
        <label htmlFor="newPass" className="">
        {t('newp')}
        </label>
        <input type="password" id="newPass" className="form-control" onChange={updateUserPass} />
        <br/>
      {/*  <label htmlFor="defaultFormLoginPasswordEx" className="grey-text">
          Confirm  New password
        </label>
        <input type="password" id="defaultFormLoginPasswordEx" className="form-control" />
        */}
        <div className="text-center mt-4">
          <MDBBtn color="success" type="submit">{t('update')}</MDBBtn>
        </div>
      </form>
    </MDBCol>
  </MDBRow>
        </div>
    )
}
