import { MDBBtn, MDBCol, MDBInput, MDBRow } from 'mdbreact'
import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useLocation } from 'react-router-dom'
import { Context } from '../../Context'
import {useTranslation} from 'react-i18next'
import {cr,apartments,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import axios from 'axios'
import Loading from '../loading'
import Ownership from '../../components/upload/ownership'
import Address from '../../components/upload/address'
import Facilities from '../../components/upload/facilities'
import Rules from '../../components/upload/rules'
import { setFormData } from './upload'
import ChangeImages from '../../components/upload/changeImg'
import Msg from '../../components/msg'
import { handleNwErr } from '../../utils/handleNetworkErr'


export default function EditHouse() {
    const {t} =useTranslation()
    const {user,dispErr}=useContext(Context)
    const lastStep=6
    const [currentStep,setCurrentStep]=useState(1)
    const [loading,setLoading]= useState(false)
    
    const [msg,setMsg]=useState('')
    const [prompt,setShowPrompt]=useState(false)
    //const [images,setImages]=useState(null)
    const [h,setHouse]=useState({})
   
    
    let location =useLocation()
    let navigate=useNavigate();

    const [item,setItem]= useState(location.state)
    
    function handleChange(e){
        setItem({
            ...item,
            [e.target.name]:e.target.value
        })
    }
   
    function setImgs(e) {
        //console.log(e.target.files[0].size);
          if (Array.from(e.target.files).length > item.MAX_LENGTH) {
              alert(`sorry. you Cannot upload files more than ${item.MAX_LENGTH}  images`);
              e.target.value=null
              return;
            }
          
            else {
              for(let i=0;i<e.target.files.length;i++){
                if((e.target.files[i]).size/1024>5120){
                 alert('each file should have a maximum size of 5 mega bytes')
                 e.target.value=null
                }
                //console.log((e.target.files[i].size)/1024);
              }
            }
          
              setItem({ 
              ...item,
              imgCollection: e.target.files 
            })
      }
      function deleteHouse(){
       // console.log(item.id);
       setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/house/delete',
           {
                id:parseInt(user.ui),
                houseId:item.id,//itemToDelete.houseId,
                
            },
           { withCredentials:true}
        ).then(
            res=>{
                 alert ('success')
               navigate(-1)
                // console.log(res.data);
         }
        ).catch(
            err=> {
                console.log(err)
                handleNwErr(err,dispErr)
               // alert('error occured')
               if(err.response){
                if(err.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
                
            }
            }
           
        ).finally(()=>{
            setLoading(false)
        })
    }
    const setMainImg=(e)=>{
        let file=e.target.files[0];
        if(file.size/1024> 5120){
         alert('each file should have a maximum size of 5 mega bytes')
         e.target.value=null;
        }
        else{
            setItem({ 
                ...item,
                mainImg:file})
        }
         
      }
      const next=(e)=>{
        e.preventDefault()
        if(currentStep<lastStep){
          setCurrentStep(currentStep+1)
        }
      }
      const prev=()=>{
        if(currentStep>1){
          setCurrentStep(currentStep-1)
        }
      }
      const setRules=(e)=>{
        let {value}=e.target
       
        setItem({
          ...item,
          rules:item.rules.includes(value)?
          item.rules.filter((rule)=>{return rule!==value}):
           [...item.rules,value]
        })    
      }
      const setFacilities=(e)=>{
        let {value}=e.target
       console.log(value)
        setItem({
          ...item,
          facilities:item.facilities.includes(value)?
          item.facilities.filter((facility)=>{return facility!==value}):
           [...item.facilities,value]
        })    
      }
    useEffect(()=>{
       
        if(location.state){
           // console.log(location.state);
            let house=location.state;
            setHouse(house)
            let facilities=house.facilities.split(',')
            let rules=house.rules.split(',')
           
            setItem({
                ...item,
                facilities:facilities,
                rules:rules,
                
                imgCollection:[],
               
            })
            console.log(item);
            
            
        }
        else{
            navigate(-1)
        }
    },[])
    function save(e){ //submit for update
        e.preventDefault()
        let formData= setFormData(item,user)
       //console.log(item);
       setLoading(true)
       axios.put(process.env.REACT_APP_ADDRESS+'/house/update',
           formData,{withCredentials:true}
       ).then(
           res=>{
              
            setMsg('success')
            setShowPrompt(true)        
           // window.location.reload(false)
           }
       ).catch(err=> {
          // alert('an error occured. ')
          console.log(err);
          handleNwErr(err,dispErr)
          if(err.response){
            if(err.response.data.auth){
                navigate('/login',{state:{msg:'session expired'}})
            }
        } 
           //alert(e.response.data)
        }).finally(()=>{
            setLoading(false)
        })
        
    }
  return (
    <div className='mh-100 ' id='upload'  style={{overflow:'scroll'}}>
        <h3 className='text-center'>{t('editHouse.h1')} <u>{item.id}</u></h3>
        <p className='text-center'>{t('editHouse.warn1')}</p> 
        <Loading show={loading}/>
        <div>
              <Ownership currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} state={item}/>
              <Address currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} state={item}/>
              <Facilities currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} setFacilities={setFacilities} state={item}/>
              <Rules currentStep={currentStep} prev={prev} next={next} handleChange={handleChange} lastStep={lastStep} setRules={setRules} state={item}/>
              <ChangeImages currentStep={currentStep} prev={prev} save={save} handleChange={handleChange} lastStep={lastStep} handleAltImgs={setImgs} handleImg={setMainImg} state={item} />

            </div>
            <Msg show={prompt} hide={()=>navigate(-1)} msg={msg}/>
    </div>
  )
}
