import React from "react";
import {  MDBRow, MDBCol, MDBIcon, MDBBtn } from "mdbreact";
import { useNavigate } from "react-router";
import { useTranslation } from 'react-i18next';
import Chat from "./chat";
import { createSearchParams } from "react-router-dom";

const AboutPage = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  return (
    <div className=" text-capitalize mh-100 mx-2 " >
    <Chat/>
        <h2 className="h1-responsive font-weight-bold text-center my-5">
         {t('about.title')}
        </h2>
        <MDBCol xs='11' lg='10' className="mx-auto">
       <MDBRow>
        
        <MDBCol  className="text-center text-lg-left   mx-auto mt-lg-3" xs='10' md="7" lg='5'>
            <div >
            <img
              className="img-fluid"
              src="images/about-img.jpg"
              alt=""
              id='about-img'
            />
            </div>
            <div className="text-lg-right text-center">
          <MDBBtn  type='submit' onClick={()=>{navigate('/guest-houses')}} size='lg' className="float-sm-right float-md-none text-center btn-purple text-white"  >
                   {t('choose your home')}</MDBBtn>
          </div>
          </MDBCol>
        <MDBCol xs="11" lg="7">
          <MDBCol xs="11" lg="12">
                <div id=''>
                  <h2 className="font-weight-bold mb-3">{t('about.about us.title')}</h2>
                  <p className="text-justify">
                  {t('about.about us.desc')}
                  </p>
                  </div>
          </MDBCol>
          
             <MDBCol xs="10" lg='12'>
                  <h2 className="font-weight-bold mb-3">{t('about.vision.title')}</h2>
                  <p className="text-justify">
                  {t('about.vision.desc')}
                  </p>
              </MDBCol>
              <MDBCol xs="10"  lg="12">
                <h2 className="font-weight-bold mb-3">{t('about.mission.title')}</h2>
                <p className="text-justify">
                {t('about.mission.desc')}
                </p>
              </MDBCol>
          
          
        </MDBCol>
        
       </MDBRow>
       </MDBCol>
      </div>
  );
}

export default AboutPage;
