import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios'
import {Link, useNavigate} from 'react-router-dom'
import {useTranslation} from 'react-i18next'
import { MDBAlert, MDBBtn, MDBCard, MDBCardBody, MDBCardGroup, MDBCardImage, MDBCardText, MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBCol, MDBContainer, MDBIcon, MDBInput, MDBListGroup, MDBListGroupItem, MDBModal, MDBModalBody, MDBModalFooter, MDBModalHeader, MDBRow, MDBView } from 'mdbreact'
import {regions,cr,apartments,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import '../style.css'
import {Context} from '../../Context'
import spinner from '../../images/spinner.svg'
import Loading from '../loading'
import DetailsModal from '../../utils/detailsModal'
import { Button, Modal } from 'react-bootstrap'


import {BsFillHouseDoorFill,BsFillEyeFill, BsFillEyeSlashFill, BsFillTrashFill,} from 'react-icons/bs'
import DelPrompt from '../../components/delPrompt'
import { handleNwErr } from '../../utils/handleNetworkErr'
export default function MyHouses() {
    let {user,dispErr} = useContext(Context)
    
    //const [houseId, setHouseId] = useState()
    const [currentPage,setCurrentPage] = useState(1)
    const [housesPerPage,setHousesPerPage]= useState(12)
    const [loading,setLoading]= useState(false)
    const [uploads,setUploads] = useState([])  
    const [visibleHomes,setVisibleHomes]=useState(0)
    const [filteredHouses,setFilteredHouses]=useState([])
    const [id2Drop,setId2Drop]=useState(0)
    const navigate = useNavigate();
    
         const [on,setOn] = useState(false);
         let modalOn =()=>{ setOn(true)}  
         let closeModal =()=>{setData(null)}
         let modalToggle =()=>{ setOn(!on)} 
         const [showPrompt,setShowPrompt]=useState(false)
    useEffect(()=>{
       
       
            setLoading(true)
           // let id=localStorage.getItem('rmg_iu')
            
            axios.post(process.env.REACT_APP_ADDRESS+'/house/uploads',{id:parseInt(user.ui)},{
            
               withCredentials:true
         }).then(res=>{
                //console.log(res.data);
                let houses=res.data.houses
                setUploads(res.data.houses)
                setFilteredHouses(houses)
                if(res.data.houses.length>0){
                  let vH=houses.filter((house)=>{
                    return house.status===1
                  })
                  setVisibleHomes(vH.length)
                }
                
            }).catch(err=> {
              handleNwErr(err,dispErr)
              console.log(err)
              if(err.response){
                if(err.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
            })
            .finally(()=>{
                setLoading(false)
              })
        }
           
        ,[])
  
    const [data,setData] = useState()
    const hideModal=()=>{
      setShowPrompt(false)
    }
    //const [houseImgs,setHouseImgs] = useState([])
    function upload(){
      if(user.isAdmin===true){
        navigate('/admin/upload')
      }
      else  navigate('/user/upload')
     
    }

    const { t } = useTranslation();
    const dropHouse =()=>{
      setShowPrompt(false)
      console.log(id2Drop)
      setLoading(true)
      
      
      axios.post(process.env.REACT_APP_ADDRESS+'/house/delete',{houseId:parseInt(id2Drop)},{withCredentials:true})
      .then(
        resp=>{
          console.log(resp.data);
          let f=uploads.filter(h=>{
            return h.id!==id2Drop
          })
         setUploads(f)
         setFilteredHouses(f)
        }
      )
      .catch(e=>{
        console.log(e);
        handleNwErr(e,dispErr)
        if(e.response){
          if(e.response.data.auth){
            navigate('/login',{state:{msg:'session expired'}})
         }
         else console.log(e.response);
        }
      })
      .finally(()=>setLoading(false))

    }
    
   
    function handleClick(event) {
        setCurrentPage(
          Number(event.target.id)
        )
      }  
    const indexOfLastHouse = currentPage * housesPerPage;
    const indexOfFirstHouse = indexOfLastHouse - housesPerPage;
    const currentHouses = filteredHouses.slice(indexOfFirstHouse, indexOfLastHouse); 
    const renderHouses = currentHouses.map((h,index)=>{
        let images= h.other_images?h.other_images.trimRight().split(' '): ""
        let mainImg=h.image?h.image:'';
         //console.log(images)
       return (
           <MDBCol xs='10' key={h.id}  lg="4" className='mx-auto'>
               <MDBCard  style={{maxHeight:'95%',height:'95%'}} >
               {mainImg.length>10?(
                <MDBCardImage src={mainImg} alt='img' className='card-img-top ' style={{height:'14rem',margin:'auto',width:'100%'}} />
             ):(
              <MDBCardImage src={images[0]} alt='img' className='card-img-top ' style={{height:'14rem',margin:'auto',width:'100%'}} />

             )}
                   <MDBCardBody>
                   
                        <p className='text-center'>{h.status===1?<BsFillEyeFill/>:<BsFillEyeSlashFill/>}</p>
                       <MDBCol md='6' xs='6' lg='8'><p>{t('uploads.ref')}: {h.id}</p>  </MDBCol>
                       <MDBCol md='12' xs='6' ><p>{t('uploads.city')}: {h.city}</p></MDBCol>
                       <MDBCol md='12' xs='12'><p>{t('uploads.type')}: {t('htype.'+h.type.toLowerCase())}</p> </MDBCol>
                       <MDBCol md='8' xs='6'><p>{process.env.REACT_APP_CUR} {h.price}</p>  </MDBCol>
                      
                      
                       <MDBBtn type='submit' gradient="purple" onClick={()=>{
                           setData(h.id)
                         
                            
                       }
                          }>{t('uploads.showmr')}</MDBBtn>
                           <MDBBtn color="primary" 
                           onClick={()=>{
                              navigate('/user/edit-house',{state:h})
                           }} >{t('edit')}</MDBBtn> 
                     
                       <button className='border-0 float-lg-end' onClick={()=>{
                        setId2Drop(h.id)
                        setShowPrompt(true)
                        }}><BsFillTrashFill color='red' size={20}/></button>
                       </MDBCardBody>
                  
               </MDBCard>
               
               <DetailsModal                  
                 images={images}
                 h={h}
                 modalToggle={modalToggle}
                 closeModal={closeModal}
               //  t={t}
                 data={data}
                  />
           </MDBCol>
       ) 
   
    })
     // Logic for displaying page numbers
    const pageNumbers = [];
   for (let i = 1; i <= Math.ceil(filteredHouses.length / housesPerPage); i++) {
     pageNumbers.push(i);
   }
    const renderPageNumbers = pageNumbers.map(number => {
        return (
          <li
            key={number}
            id={number}
            onClick={handleClick}
           // className="rounded-circle border p-1"
            className={currentPage===parseInt(number)?'fw-bold rounded-circle border p-1 text-danger':'fw-bold rounded-circle border p-1 '}
          >
            {number}
          </li>
        );
      });

      const handleFilter =(e)=>{
        let val=e.target.value.trim()
        let search=uploads.filter(upload=>{
          let city=upload.city.toLowerCase(),
          location=upload.location.toLowerCase()

          return city.includes(val) || location.includes(val.toLowerCase())
        })
        setFilteredHouses(search)
      }
    return (
        <div id='myUploads' style={{overflow:'scroll',maxHeight:'100vh'}}>
         <Loading show={loading}/>
            <h2 className='text-center mb-6 text-capitalize'>{t('myUploads')}</h2>
           
           
            {/* <img src={('http://localhost:3001/images/key-2114044_1280.jpg1614249511144.jpeg')} alt='img'/>*/}
            {loading===false && uploads.length<1?  (
                <MDBContainer style={{height:'90vh'}}>
                <h2 className='text-center '>{t('nuy')} <Link to='/user/upload'>{t('upl')} </Link></h2>
                </MDBContainer>
            ):(
                <div>
                <MDBContainer fluid style={{minHeight:'100vh'}}>
                <Button className='d-block mx-auto' onClick={()=>upload()}>{t('choose.add')}</Button>
                <Modal show={showPrompt} onHide={hideModal}>
                <Modal.Header closeButton/>
                <Modal.Body>
                  <p className='text-center'>{t('drop_question')} <u><b>{id2Drop}</b></u></p>
                </Modal.Body>
                <Modal.Footer>
                  <Button onClick={hideModal}>{t('cancel')}</Button>
                  <Button variant='danger' onClick={dropHouse}>{t('del')}</Button>
                </Modal.Footer>
                </Modal>
                <MDBCol xs={9} md={3} className='text-center mx-auto'>
                  <p><BsFillHouseDoorFill/><b>{uploads.length}</b></p>
                  <p><BsFillEyeFill/>:<b>{visibleHomes}</b></p>
                  
                 <input type='text' className='form-control mb-2' placeholder='filter ' onChange={handleFilter}/></MDBCol>
               <p className='text-center'>page {currentPage} of {Math.ceil(uploads.length / housesPerPage)}</p>
              <MDBRow> 
                  {renderHouses}
               </MDBRow>
               <ul id="page-numbers" className='text-center mx-auto'>
                    {renderPageNumbers}
                 </ul>
                </MDBContainer>
               
                </div>
            )}
        </div>
    )
}




