import React, { useState } from "react";
import {  MDBContainer, MDBRow, MDBCol, MDBIcon, MDBBtn, MDBInput, MDBAlert } from "mdbreact";
import {useTranslation} from 'react-i18next'
import axios from "axios";
import { set } from "js-cookie";
import Loading from "./loading";
import Chat from "./chat";
const Contact = () => {
  const { t } = useTranslation();
  const [smsg,setSmsg]= useState('')
  const [loading,setloading]=useState(false)
  const [mail,setMail] = useState({
    email:'',
    subject:'',
    message:''
  })
  function sendMsg(e){
    e.preventDefault();
    setloading(true)
    axios.post(process.env.REACT_APP_ADDRESS+'/house/message',{
      email:mail.email,subject:mail.subject,message:mail.message
    }).then(
      res=>{
        setSmsg(t('thxs'))
        setMail({
          email:'',
          subject:'',
          message:''
        })
      }
    ).catch(
      err=> console.log(err)
    )
    .finally(()=>setloading(false))
   
  }
  return (
    <MDBContainer>
      <h2 className="h1-responsive font-weight-bold text-center my-5">
        {t('cu')}
      </h2>
      <p className="text-center w-responsive mx-auto pb-5">
       {t('serv')}
      </p>
      <MDBRow className='text-capitalize'>
        <MDBCol md="9" className="md-0 mb-5">
        {smsg!=='' && (
          <MDBAlert dismiss className='text-center text-capitalize'>
            <p> {smsg}</p>
          </MDBAlert>
        )}
          <form onSubmit={ 
             sendMsg
          }>
            <MDBRow>
              <MDBCol md="6">
                <div className="md-form mb-0">
                  <MDBInput type="text" id="contact-name" label={ t('name')} required/>
                </div>
              </MDBCol>
              <MDBCol md="6">
                <div className="md-form mb-0">
                  <MDBInput
                    type="email"
                    id="contact-email"
                    label={ t('email')} 
                    required
                    name='email'
                    value={mail.email}
                    onChange={(e)=>{
                      setMail({
                        ...mail,
                        email:e.target.value
                      })
                    }}
                  />
                </div>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <div className="md-form mb-0">
                  <MDBInput type="text" id="contact-subject" label={t('subject')} 
                  required 
                  value={mail.subject}
                  onChange={(e)=>{
                      setMail({
                        ...mail,
                        subject:e.target.value
                      })
                    }}/>
                </div>
              </MDBCol>
            </MDBRow>
            <MDBRow>
              <MDBCol md="12">
                <div className="md-form mb-0">
                  <MDBInput
                    type="textarea"
                    id="contact-message"
                    label={ t('message')}
                    required
                    value={mail.message}
                    onChange={(e)=>{
                      setMail({
                        ...mail,
                        message:e.target.value
                      })
                    }}
                  />
                </div>
              </MDBCol>
            </MDBRow>
            <div className="text-center text-md-left">
            <MDBBtn color="primary" size="md" type='submit' disabled={loading}>
              {t('send')}
            </MDBBtn>
          </div>
          </form>
          <Loading show={loading}/>
        </MDBCol>
        <MDBCol md="3" className="text-center">
          <ul className="list-unstyled mb-0">
            <li>
              <MDBIcon icon="map-marker-alt" size="2x" className="blue-text" />
              <p>Yaounde omnisport (Opposite amadou ahidjo stadium) </p>
            </li>
            <li>
              <MDBIcon fab icon="whatsapp" size="2x" className="green-text mt-4" />
              <p>+ 237 6 517 054 06</p>
            </li>
            <li>
              <MDBIcon icon="envelope" size="2x" className="blue-text mt-4" />
              <p>social@residencemg.co</p>
            </li>
          </ul>
        </MDBCol>
      </MDBRow>
      <Chat/>
    </MDBContainer>
  );
}

export default Contact;