import React, { useState } from 'react'
import { Button, Form,Row,Col, Modal, Carousel, Card } from 'react-bootstrap'
import { useTranslation } from 'react-i18next';

export default function Images({currentStep,prev,handleChange,lastStep,handleImg,handleAltImgs,save,state}) {
    const [showOld,setShowOld]=useState(false)
    const show=()=>setShowOld(true)
    const hide=()=>setShowOld(false)

    const {t}=useTranslation()
    if(currentStep!==5){
        return null;
    }
  return (
    <div className='card border-0 col col-md-8 mx-auto' id='images'>
        {/*<Button onClick={show}>see current Images</Button>
        <ShowImages main={state.images.main} otherImgs={state.images.imgUrl} show={showOld} hide={hide}/>*/}
        <Form onSubmit={save}>
        <h4 className='text-center'> { t('choose.img_subh') } </h4>
        <Col md={8} className='mx-auto'>
        <Col>
        <Form.Group>
            <h5>{t('choose.mainImg')}</h5>
            <Form.Label>{t('choose.mainImg_desc')}</Form.Label>
            <Form.Control  type="file" accept='image/jpeg,image/png'  name='image' onChange={handleImg} required></Form.Control>
        </Form.Group>
        </Col>
        <Col>
        <Form.Group>
        <h5>{t('choose.imgs')}</h5>
            <Form.Label>{t('choose.imgs_desc')}</Form.Label>
            <Form.Control  type="file"  accept='image/jpeg,image/png' multiple  name='imgCollection' onChange={handleAltImgs} required></Form.Control>
        </Form.Group>
        </Col>
        
            <h4 className='text-center'>{''}</h4>
        <Col>
        <Form.Group>
            <h5>{t('choose.pricing')}</h5>
            <Form.Label>{t('choose.price')} ?</Form.Label>
            <Form.Control type='number' required name='price' onChange={handleChange} defaultValue={state.price}></Form.Control>
        </Form.Group>
        
        </Col>
        <Col>
        <Form.Group>
            <Form.Label>{t('choose.disc_price')} ?</Form.Label>
            <Form.Control type='number' required name='discPrice' onChange={handleChange} defaultValue={state.discPrice}></Form.Control>
        </Form.Group>
        </Col>
        </Col>
       <div className='text-center'>
        <Button onClick={prev} disabled={currentStep<=1?true:false}>{t('choose.prev')}</Button>
              <Button type='submit'>{t('choose.submit')}</Button>
              </div>
        </Form>
    </div>
  )
}

