import React,{useState,useEffect, useContext} from 'react'
import axios from 'axios';

import {useTranslation} from 'react-i18next';
import DetailsModal from '../../utils/detailsModal';

import Loading from '../loading';
import { Button, Form } from 'react-bootstrap';

import Chat from '../chat';
import HousesDisp from '../../components/houseDisplay';
import { Context } from '../../Context';
import { handleNwErr } from '../../utils/handleNetworkErr';

export default function GuestHouse() {
    const [houses,setHouses]=useState([])
    const [fhouses,setFhouses]=useState([])
   
   const [loading,setloading]=useState(false)

    
    const { t } = useTranslation();
    const {dispErr}=useContext(Context)
      
    //to apply filter to the houses based on city and type
    
    function filterByCity(val) {
       
     //console.log(val);
          if(val){
            let f= houses.filter(house=>
              {
                    return house.city.toLowerCase()===val.toLowerCase() 
                })
            setFhouses(f)
           console.log(f);
        }
        else{
            
            setFhouses(houses)
        }

    }
    function filterByType(val) {
        console.log(val);
        if(val){
            const f= fhouses.filter(house=>
                {
                    return house.type.toLowerCase()===val.toLowerCase() 
                })
            setFhouses(f)
        }
       
    }
    
    useEffect(()=>{
        setloading(true)
        //process.env.REACT_APP_ADDRESS
        axios.get(process.env.REACT_APP_ADDRESS+'/guests/test').then(
            (resp)=>{
               //console.log(resp.data);
               // if(Array.isArray(resp.data)){
                    setHouses(resp.data)
                    setFhouses(resp.data)
              //  }
               // console.log(process.env.REACT_APP_CUR);
            }
        ).catch(
            err=>{
                console.log(err)
                /*if(!err.response){
                    dispErr()
                }*/
                handleNwErr(err,dispErr)
            }
        )
        .finally(
            ()=>setloading(false)
        )
    },[])
   

    return (
        <div className='container-fluid mh-100' >
       
        <Chat/>
            <h1 className='text-center'>{t('ghouses')} </h1>
            <Loading show={loading} />
            {
             houses.length<1 && loading===false?  (
                <p className='text-center'> {t('noGhouses')}</p>
             ):(
                <div>
                <div className="p-2 text-center mb-3 ">
                {/*<select onChange={(e)=>{
                    filterByCity(e.target.value);
                    
                    }} className='mx-2'>
                    <option value=''>{t('uploads.city')}</option>
                    <option value="Yaounde">Yaounde</option>
                    <option value="Douala">Douala</option>
                    <option value="Limbe">Limbe</option>

                    
                </select>
                <select onChange={(e)=>{
                    filterByType(e.target.value)
                }}>
                    <option value={''}>{t('uploads.type')}</option>
                    <option value="apartment">apartment</option>
                    <option value="studio">Studio</option>
                    <option value='studio'></option>
                    
                </select>
                 <Button onClick={()=>setFhouses(houses)}>{t('clearFilter')}</Button>
                */}
               
            </div>
            {fhouses.length>0 && (
                <HousesDisp houses={fhouses} hpp={6} showCity={true}/>
            )}
                
                </div>            
             )   
            }
            
        </div>
    )
}




