import axios from 'axios'
import { MDBBtn, MDBCard, MDBCardBody, MDBCardFooter, MDBCardImage, MDBCardText, MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBCol, MDBContainer, MDBIcon, MDBInput, MDBListGroup, MDBListGroupItem, MDBModal, MDBModalBody, MDBModalFooter, MDBModalHeader, MDBRow, MDBView } from 'mdbreact'
import React, { useContext,useState ,useEffect} from 'react'

import { useNavigate } from 'react-router'
import {useTranslation} from 'react-i18next'
import {Context} from '../../Context'
import '../style.css'
import AdminMenu from './adminMenu'
import {regions,cr,apartments,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import Loading from '../loading'
import PrompDeleteModal from '../../utils/prompDeleteModal'
import { FaEye } from 'react-icons/fa'
import { BsFillHouseDoorFill } from 'react-icons/bs'
import { handleNwErr } from '../../utils/handleNetworkErr'
//import Loading from '../../utils/loading'

export default function AdminView() {
    const instance = axios.create({
        withCredentials: true
    })
    const navigate = useNavigate()
    const {user,dispErr}=useContext(Context)
    //const {ui,setUi}=useContext(AuthContext)
    const [houses,setHouses] = useState([])
    const [modal,setModal]= useState(false)
    const [status,setStatus]= useState()
    const [houseId, setHouseId] = useState()
    const {t}= useTranslation()
    const [loading,setLoading]= useState(false)
    const [showDropModal,setShowDropModal]= useState(false)
    const [item2Drop,setItem2Drop]=useState(null)
    const [item,setItem] =useState({
        price:0,
        type:'',
        description:'',
        location:'',
        city:'',
        region:'',
        rooms:0,
        toilets:0,
        parking:'no',
        furniture:'no',
        kitchen:'',
        houseId:0,
        owner:0,
        is_guest_house:0,
        vip:0,
        images:[],
        agent_num:0,
        MAX_LENGTH:12,
        imgCollection:[]
      
    })
    const [filteredHouses,setFilteredHouses]=useState([])
    const [selected,setSelected]= useState('')
    const [currentPage,setCurrentPage] = useState(1)
    const [housesPerPage,setHousesPerPage]= useState(12)
    
   const [editmodal,seteditmodal]= useState(false)
   const [editItemmodal, setEditItemmodal]= useState(false)
    let modalToggleChangeStatus =()=>{ setModal(!modal)}
    let modalToggleViewMore =()=>{ 
       // setHouseId(null)
        seteditmodal(!editmodal)

    }
    let modalTogleItemEdit= ()=>{setEditItemmodal(!editItemmodal)}
    function handleClick(event) {
        setCurrentPage(
          Number(event.target.id)
        )
      }
      const handleFilter =(e)=>{
        let val=e.target.value.trim()
        let search=houses.filter(upload=>{
          let city=upload.city.toLowerCase(),
          location=upload.location.toLowerCase()

          return city.includes(val) || location.includes(val.toLowerCase())
        })
        setFilteredHouses(search)
      }
    useEffect(()=>{
     
        setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/house/all',{id:parseInt(user.ui)},{withCredentials:true})
       // axios.post(process.env.REACT_APP_ADDRESS+'/houses',{id:parseInt(user.ui)},{withCredentials:true})
        .then(res=>{
         
            setHouses(res.data)
            setFilteredHouses(res.data)
            //
        })
        .catch(err=>{
             console.log(err);
             handleNwErr(err,dispErr)
             if(err.response){
                 if(err.response.data.auth){
                     navigate('/login',{state:{msg:'session expired'}})
                 }
                 console.log(err.response.data);
             }
             
             })
             .finally(()=>setLoading(false))
   
    },[])

     
     
      //edit of house status by admin
    function editStatus(e){
        e.preventDefault();
//console.log(houseId);
       // console.log(status);
       setModal(false)
       setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/house/edit',{
            id:parseInt(user.ui),
            houseId:houseId,
            status:status
    },{withCredentials:true}).then(
        res=>{
            console.log(res.data);
            window.location.reload(false);
        }
    ).catch(
        err=>{
            handleNwErr(err,dispErr)
            console.log(err)
            if(err.response){
                if(err.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }

        }
    )
    .finally(()=>setLoading(false))
    }
    //delete house
    function promptModal(id){
        setItem2Drop(id);
        setShowDropModal(true)
    }
   function deleteHouse(){
        setShowDropModal(false)
       setLoading(true)
       axios.post(process.env.REACT_APP_ADDRESS+'/house/delete',{
           
               id:parseInt(user.ui),
               houseId:item2Drop,
            
           },
           {withCredentials:true}
       ).then(
           res=>{
                alert (res.data)
                //window.location.reload(false)
                let h=houses.filter(house=>{
                    return house.id!==item2Drop
                })
                setHouses(h)
            setFilteredHouses(h)

        }
       ).catch(
           err=> {
            handleNwErr(err,dispErr)
               console.log(err)
               if(err.response){
                if(err.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
            }
       )
       .finally(()=>{
           setItem2Drop(null)
           setLoading(false)
        })
      // console.log(houseImgs);
   }
   // Logic for displaying current todos
   const indexOfLastHouse = currentPage * housesPerPage;
   const indexOfFirstHouse = indexOfLastHouse - housesPerPage;
   const currentHouses = filteredHouses.slice(indexOfFirstHouse, indexOfLastHouse);
   const renderHouses = currentHouses.map((house, index) => {
    let imgs= house.other_images.trimRight().split(' ')
    let mainImg=house.image;

    return (
        <MDBCol md='5' lg='3' xs='11' key={index} className='mx-auto' style={{minWidth:'250px'}}>
            <MDBCard style={{ width: "90%",maxHeight:'90%',height:'90%' }}>
            {mainImg.length>15?(
                <MDBCardImage src={mainImg} alt='img' className='card-img-top ' style={{height:'30vh',margin:'auto',width:"90%"}} />
             ):(
                <MDBCardImage src={imgs[0]} alt="Card image" style={{height:'30vh',margin:'auto',width:'90%'}} className='card-img-top' />

             )}
            
             <MDBCardBody>
             
             <p className='fw-bold text-center'><FaEye/> {house.views}</p>
                        
                   
                <MDBRow>
                <MDBCol md='5' xs='6' >
                    <p className="">{t('uploads.ref')}: {house.id}</p>
                    </MDBCol>
                    <MDBCol md='5' xs='6' >
                    <p className="">{house.city}</p>
                    </MDBCol>
                    <MDBCol md='6' xs='2'>
                    <p className="">{process.env.REACT_APP_CUR} {house.price}</p>
                    </MDBCol>
                    {house.status===0? (
                        <MDBCol md='6' xs='2'>
                    <p className="">{t('uploads.status')}: Occupied</p>
                    </MDBCol> ):(
                        <MDBCol md='6' xs='2'>
                    <p className="">{t('uploads.status')}: Vacant</p>
                    </MDBCol>
                    )}
                  
                    
                </MDBRow>
                <MDBModal isOpen={modal} size='md' toggle={modalToggleChangeStatus}>
                <MDBModalHeader toggle={modalToggleChangeStatus}>
                   {t('uploads.edst')} id {houseId}
                </MDBModalHeader>
                <MDBModalBody>
                <form onSubmit={editStatus}>
                <label htmlFor='#status-select'>Select New Status</label>
                    <select className="custom-select custom-select-sm"
                    id='status-select'
                     required onClick={(e)=>{
                         setStatus(e.target.value)
                        // console.log(status)
                         }}>
                    <option ></option>
                    <option value={1} selected={house.status===1}>Vacant</option>
                    <option value={0} selected={house.status===0}>Occupied</option>
                
                    </select>
                    <MDBBtn type='submit' disabled={loading}>Save Changes</MDBBtn>
                    <MDBBtn onClick={modalToggleChangeStatus}>close</MDBBtn>
                    
                    </form>
                        </MDBModalBody>
                </MDBModal>
            {/**   
                     */} 
                     <MDBModal isOpen={editItemmodal} toggle={modalTogleItemEdit}>
                        <MDBModalHeader toggle={modalTogleItemEdit}>Edit item with house Id {houseId}</MDBModalHeader>
                        
                     </MDBModal>
                    <MDBCol size='12'  className='mx-auto'>
             <MDBBtn onClick={()=>{
                 setHouseId(house.id)
                
                 
                seteditmodal(true)
             }
             } >View More</MDBBtn></MDBCol>
             <MDBCol size='12'  className='mx-auto'>
             <MDBBtn onClick={()=>{
                navigate('/admin/edit-house',{state:house})
                 setEditItemmodal(true)
             }
             } >Edit Item</MDBBtn></MDBCol>
             <MDBCol size='12'>
                 <MDBBtn color='red' onClick={()=>{
                    
                     promptModal(house.id)
                 }}>
                 Trash<MDBIcon icon="trash" /></MDBBtn>
             </MDBCol>
                <MDBCol size='12'><MDBBtn type='submit'
                    onClick={(e)=>{
                        e.preventDefault()
                        setModal(true)
                        setHouseId(house.id)
                    }}
                    disabled={loading}
                    >Edit Status</MDBBtn>
                   </MDBCol>
              
            </MDBCardBody>
            <MDBCardFooter >
            
            </MDBCardFooter>
            
            </MDBCard>
            <PrompDeleteModal isOpen={showDropModal} action={deleteHouse} toggle={()=>setShowDropModal(false)} hId={item2Drop}/>

            <MDBModal isOpen={houseId===house.id && (editmodal===true)} toggle={modalToggleViewMore}   >
             <MDBModalHeader className='text-center text-capitalize' toggle={modalToggleViewMore}>house Details</MDBModalHeader>
                <MDBModalBody>
                <MDBRow className='bannerimg mx-auto justify-content'>
                        
                        {imgs.length<2 && 
                        <img src={imgs[0]} alt='img' style={{width:'100%',height:'40vh',margin:'auto'}}/>
                        }
                        {imgs.length>1 &&(
                            <MDBCarousel
                            activeItem={1}
                            length={imgs.length}
                            showControls={true}
                            showIndicators={false}
                            className="z-depth-1 mx-auto"
                            slide
                            >
                            
                            <MDBCarouselInner >
                            
                            {  imgs.map((ims,index)=>{
                                return ( 
                                <MDBCarouselItem itemId={imgs.indexOf(ims)+1} key={index}>
                                <MDBView>
                                    <MDBRow  id='bannerim' style={{height:'35vh'}}>
                                    <img src={ims} className='' alt='images' style={{maxHeight:'100%',margin:'auto',width:'45vh',height:'85vh', maxWidth:'45vh'}}/>
                                    </MDBRow>
                                </MDBView>                                               
                                </MDBCarouselItem>
                                )
                            })}                                            
                        </MDBCarouselInner>                                                
                        </MDBCarousel>
                            
                            )}                       
                </MDBRow>
                        <br/>                    
                <div id='dmbody' style={{marginTop:'0'}}> 
              <p className='text-center'>LandLord's Number:{house.landlord}</p>
                <MDBRow >
                    <MDBCol lg='8' xs='10' className='mx-auto'>
                    <h3>Address:</h3>
                        <MDBListGroup>
                            <MDBListGroupItem>
                            <p>City: {house.city}</p>
                            <p>Quater: {house.location}</p>
                            </MDBListGroupItem>
                            
                        </MDBListGroup>
                    </MDBCol>
                    <MDBCol lg='4' xs='10'>
                    <h3 className='fs-5'>Pricing </h3>
                    <MDBListGroupItem><p> Price: {process.env.REACT_APP_CUR} {house.price}</p></MDBListGroupItem>
                    <MDBListGroupItem><p> Discount Price: {process.env.REACT_APP_CUR} {house.discPrice}</p></MDBListGroupItem>
                    
                    </MDBCol>
                </MDBRow>
                <MDBCol md='6' xs='10' className='mx-auto'>
                    <h3>House Info</h3>
                    <MDBListGroupItem>
                    <p>Id:{house.id}</p>
                    <p>Type: {house.type}</p>
                    </MDBListGroupItem>
                  
                    <MDBListGroupItem>
                    
                        <p>Agent's Phone Number: {house.user}</p>
                        <p>Agent's Full Name: {house.fullname}</p>
                    </MDBListGroupItem>
                </MDBCol>
                <br/>
                <MDBRow className='mx-auto'>
                
                <MDBCol lg='10' xs='10' className='mx-auto'>
                <h3 className='text-cener'>Properties</h3>
                        <MDBListGroupItem className=""><p>Type: {house.type}</p></MDBListGroupItem>
                        <MDBListGroupItem className=""><p>Rooms:{house.rooms} </p></MDBListGroupItem>
                        <MDBListGroupItem className=""><p>Living Rooms:{house.livrooms} </p></MDBListGroupItem>
                        <MDBListGroupItem className=""><p>balconies:{house.balconies} </p></MDBListGroupItem>
                        <MDBListGroupItem className=""><p>Guests Allowed:{house.guestnum} </p></MDBListGroupItem>
                        <MDBListGroupItem className=""><p>Toilets: {house.toilets}</p> </MDBListGroupItem>
                        
                    
                </MDBCol>
                  
                </MDBRow>
                <br/>   
                    
                    
                    
                    
                    {house.description!==null &&(
                        <MDBCol md='10' xs='10' className='mx-auto'>
                        <h3 >Description</h3>
                        <p className="">{house.description}</p>
                        </MDBCol>
                    )}
                   </div> 
                </MDBModalBody>
            <MDBModalFooter>
            <MDBBtn onClick={modalToggleViewMore}>Close</MDBBtn>
            </MDBModalFooter>
            </MDBModal>
        </MDBCol>
    )
  });
   // Logic for displaying page numbers
   const pageNumbers = [];
   for (let i = 1; i <= Math.ceil(filteredHouses.length / housesPerPage); i++) {
     pageNumbers.push(i);
   }
   const renderPageNumbers = pageNumbers.map(number => {
    return (
      <li
        key={number}
        id={number}
        onClick={handleClick}
        className={currentPage===parseInt(number)?'fw-bold rounded-circle border p-1 text-danger':'fw-bold rounded-circle border p-1 '}
      >
        {number}
      </li>
    );
  });
    return (
        <>
        <div id='myUploads' style={{overflow:'scroll initial',maxHeight:'100vh'}}>

            <h2 className='text-center'>All Houses</h2>
            <p className='text-center'>page {currentPage} of {Math.ceil(houses.length / housesPerPage)}</p>
           <Loading show={loading}/>
           <MDBCol xs={9} md={3} className='text-center mx-auto'>
                  <p>Total Number of Houses:<b>{houses.length}</b></p>
                  <p>Total Visible:<b>{(houses.filter(house=> house.status===1).length)}</b></p>
                  
                 <input type='text' className='form-control mb-2' placeholder='filter ' onChange={handleFilter}/></MDBCol>
            <MDBRow>
               
                <MDBCol  style={{marginTop:'1.5em'}}>
                    {(houses.length>0)&&(
                    <React.Fragment>
                    <MDBRow >
                    {renderHouses}
                    </MDBRow>
                    
                    </React.Fragment>
                    
                
                )}
                </MDBCol>
            </MDBRow>
            <ul id="page-numbers">
                        {renderPageNumbers}
                    </ul>
        </div>
        
        </>
    )
}
