
import React, { useContext, useEffect, useState } from 'react'
//import { useHistory } from 'react-router'
import axios from 'axios'
import { MDBBtn, MDBCol, MDBContainer, MDBRow, MDBTable, MDBTableBody, MDBTableHead,MDBDataTable } from 'mdbreact'
import {Context}  from '../../Context'
import AdminMenu from './adminMenu'
import Loading from '../loading'
import { useNavigate } from 'react-router-dom'
import DelPrompt from '../../components/delPrompt'
import { Button, Modal } from 'react-bootstrap'
import { handleNwErr } from '../../utils/handleNetworkErr'


export default function Users() {
//const history = useHistory()
let {user,dispErr}= useContext(Context)
const [loading,setLoading] = useState(false)
const [currentPage,setCurrentPage] = useState(1)
const [usersPerPage,setUsersPerPage]= useState(20)
const [showPromptDel,setShowPromptDel]=useState(false)
const navigate=useNavigate();
    useEffect(()=>{
        
            setLoading(true)
            axios.post(process.env.REACT_APP_ADDRESS+'/user/all',{id:parseInt(user.ui)},{withCredentials:true}).then(
            res=>{
                //console.log(res.data)
                setUsers(res.data)
            }
        ).catch(e=>{
            handleNwErr(e,dispErr)
            if(e.response){
                if(e.response.data.auth){
                    navigate('/login',{state:{msg:'session expired'}})
                }
            }
        })
        .finally(
            ()=>setLoading(false)
        )
        
    },[])
    const [users,setUsers] = useState([])
    const [user2Drop,setUser2Drop]= useState({
        id:0,
        telephone:0
    })
    function handleClick(event) {
        setCurrentPage(
          Number(event.target.id)
        )
      }
      // Logic for displaying current todos
   const indexOfLastUser = currentPage * usersPerPage;
   const indexOfFirstUser = indexOfLastUser - usersPerPage;
   const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);
   const renderUsers = currentUsers.map((user, index) => {
    return (
        <tr key={index}>
       
        <td>{user.fullname}</td>       
        <td>{user.city}</td>
        <td>{user.email}</td>
        <td>{user.telephone}</td>
        
        <td><MDBBtn onClick={()=>{
            setUser2Drop({
                id:user.id,
                telephone:user.telephone
            })
            setShowPromptDel(true)
           

        }                     
        }>Delete</MDBBtn></td>
       
        <td><MDBBtn>Suspend</MDBBtn></td>
    </tr>
    )
   })
   // Logic for displaying page numbers
   const pageNumbers = [];
   for (let i = 1; i <= Math.ceil(users.length / usersPerPage); i++) {
     pageNumbers.push(i);
   }
   const renderPageNumbers = pageNumbers.map(number => {
    return (
      <li
        key={number}
        id={number}
        onClick={handleClick}
      >
        {number}
      </li>
    );
  });
  const hidePrompt=()=>{
    setShowPromptDel(false)
  }
    const dropUser=()=>{
        hidePrompt()
        setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/user/admin/drop',{
            userId:user2Drop.id,
            telephone:user2Drop.telephone,
           // id:parseInt(user.ui)
         },{withCredentials:true}).then(
             res=>{
                 //alert(res.data)
                //window.location.reload(false)
                let updatedUsers=users.filter(u=>{
                    return u.id!==user2Drop.id
                })
                setUsers(updatedUsers)
                setUser2Drop({
                    id:0,
                    telephone:0
                })
             }
         ).catch(
             e=> {
                handleNwErr(e,dispErr)
                 console.log(e);
                if(e.response){
                    if(e.response.data.auth){
                        navigate('/login',{state:{msg:'session expired'}})
                    }
                }
             }
         )
         .finally(()=>setLoading(false))
     }
    return (
        <div id='contaier' style={{overflowY:'scroll',minWidth:'100vw'}}>
        <Loading show={loading}/>
        <h3 className='text-center'>All users</h3>
            <MDBCol  >
            
            {loading===true &&(
                <div className="spinner-border text-center" > </div>
            )}
            <MDBTable className='table d-block mx-md-auto'  responsiveSm>
           
                <MDBTableHead>
                    <tr>
                        <th>Full Name</th>
                       
                        <th>City</th>
                       
                        <th>Email</th>
                        <th>Telephone</th>
                        <th></th>
                    </tr>
                </MDBTableHead>
                <MDBTableBody>
                
                {renderUsers}
              
               
                    
                </MDBTableBody>
            </MDBTable>
            {/*<DelPrompt action={dropUser()} showModal={showPromptDel} hide={()=>setShowPromptDel(false)} question={'Do you want to delete user named '+user.fullname+'?'}/>*/}
            <Modal show={showPromptDel} onHide={hidePrompt}>
                <Modal.Header closeButton/>
                <Modal.Body>
                    <p className='text-center'>Do you want to delete user with phone {user2Drop.telephone}?</p>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant='danger' onClick={dropUser} >Proceed</Button>
                    <Button variant='primary' onClick={hidePrompt}>Cancel</Button>
                </Modal.Footer>
            </Modal>
            </MDBCol>
            <ul id="page-numbers">
                {renderPageNumbers}
            </ul>
            
        </div>
    )
}
