import { MDBBtn, MDBModal, MDBModalBody, MDBModalFooter, MDBModalHeader } from 'mdbreact'
import React from 'react'
import { useTranslation } from 'react-i18next'

export default function PrompDeleteModal({isOpen,toggle,action,hId}) {
    const {t}=useTranslation();
  return (
    <MDBModal isOpen={isOpen} toggle={toggle}>
    <MDBModalHeader toggle={toggle}></MDBModalHeader>
    <MDBModalBody>
      <p className='text-center'>{t('ask2DropHouse')}{' '} {hId} ?</p>
    </MDBModalBody>
    <MDBModalFooter>
      <MDBBtn color="secondary" onClick={toggle}>{t('no')}</MDBBtn>
      <MDBBtn color="red" onClick={action}>{t('yes')}</MDBBtn>
    </MDBModalFooter>
  </MDBModal>
  )
}
