import axios from 'axios'
import { MDBBtn, MDBCol, MDBContainer, MDBRow, MDBTable, MDBTableBody, MDBTableHead,MDBCard, MDBDataTable } from 'mdbreact'
import React, { useEffect, useState ,useContext} from 'react'
import { useNavigate } from 'react-router'
import {Context} from '../../Context'
import Loading from '../loading'
import './admin.css'
import AdminMenu from './adminMenu'

export default function Admins() {
    const navigate = useNavigate()
let {user,setUser}= useContext(Context)
const [currentPage,setCurrentPage] = useState(1)
const [usersPerPage,setUsersPerPage]= useState(6)
const [loading,setLoading] = useState(false)
    useEffect(()=>{
        if(user.role!=='admin') navigate();
        else{
            setLoading(true)
            axios.post(process.env.REACT_APP_ADDRESS+'/admins',{id:parseInt(user.ui)},{withCredentials:true}).then(
            res=>{
               // console.log(res.data)
                setAdmins(res.data)
            }
        ).catch(er=>{
            alert('a network error occured. Try refreshing the page')
            window.location.reload(false)
        }).finally(()=>setLoading(false))
        }
        
    },[])
    const [admins,setAdmins] = useState([])
    const [user2Drop,setUser2Drop]= useState({
        id:0,
        telephone:0
    })
    function handleClick(event) {
        setCurrentPage(
          Number(event.target.id)
        )
      }
      // Logic for displaying current todos
   const indexOfLastUser = currentPage * usersPerPage;
   const indexOfFirstUser = indexOfLastUser - usersPerPage;
   const currentUsers = admins.slice(indexOfFirstUser, indexOfLastUser);
   const renderUsers = currentUsers.map((admin, index) => {
    return (
        <tr key={index}>
       
        <td>{admin.fullname}</td>
        <td>{admin.username}</td>
        <td>{admin.city}</td>
        <td>{admin.address}</td>
        <td>{admin.email}</td>
        <td>{admin.telephone}</td>
        
        <td><MDBBtn disabled={admins.length<2?true:false}
        onClick={()=>{
          /*  setUser2Drop({
                id:admin.id,
                telephone:admin.telephone
            })*/
            dropUser(admin.id, admin.telephone)
        }}>Delete</MDBBtn></td>
        <td><MDBBtn>Suspend</MDBBtn></td>
    </tr>
    )
   })
   // Logic for displaying page numbers
   const pageNumbers = [];
   for (let i = 1; i <= Math.ceil(admins.length / usersPerPage); i++) {
     pageNumbers.push(i);
   }
   const renderPageNumbers = pageNumbers.map(number => {
    return (
      <li
        key={number}
        id={number}
        onClick={handleClick}
      >
        {number}
      </li>
    );
  });
    function dropUser(id,tel){
        setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/admin/user/drop',{
            userId:id,
            telephone:tel,
            id:parseInt(user.ui)
         },{withCredentials:true}).then(
             res=>{
                 alert(res.data)
                window.location.reload(false)
             }
         ).catch(
             e=> {
                if(e.response){
                    if(e.response.data.auth){
                        navigate('/login',{state:{msg:'session expired'}})
                    }
                }
                 }
         ).finally(()=>setLoading(false))
     }
    return (
        <div id='container'>
        <Loading show={loading}/>
        <MDBRow>
            <MDBCol md='2' xs='10' sm='10'>
            <AdminMenu/>
            </MDBCol>
            <br/>
            <MDBCol sm='10' md='10' xs='10' style={{position: 'relative',top:'5vh'}} >
            <h3 className='text-center'>All Admins</h3>
            {loading===true &&(
                <div className="spinner-border text-center" > </div>
            )}
          
            <MDBTable className='table' scrollY maxHeight='40vh' >
           
                <MDBTableHead>
                    <tr>
                        <th>Full Name</th>
                        <th>Username</th>
                        <th>City</th>
                        <th>Address</th>
                        <th>Email</th>
                        <th>Telephone</th>
                        <th></th>
                    </tr>
                </MDBTableHead>
                <MDBTableBody >
               
                {renderUsers}
               
               
                </MDBTableBody>

            </MDBTable>
            <ul id="page-numbers">
                {renderPageNumbers}
            </ul>
            </MDBCol>
        </MDBRow>
            
        </div>
    )
}
