import React, { useContext, useEffect, useState } from "react";
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBCard, MDBCardBody, MDBAlert, MDBModal, MDBTooltip } from 'mdbreact';
import axios from "axios";
import { Link, NavLink, useNavigate } from "react-router-dom";
import {useTranslation} from 'react-i18next'
import spinner from '../../images/spinner.svg'
import {regions,cr,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import './styles.css'
import {Context} from "../../Context";
import Loading from "../loading";
import { Form } from "react-bootstrap";
import { FaLocationArrow } from "react-icons/fa";
import SignupPartOne from "../../components/signup-a";
import { handleNwErr } from "../../utils/handleNetworkErr";
//const {}



const FormPage = () => {
  const navigate = useNavigate();
  const {t}= useTranslation()
  const [username,setUsername] = useState('')
  const [email,setEmail] = useState('')
  const [pw,setPw] = useState('')
  const [fullName,setFullName] = useState('')
  const [cities,setCities]=useState([])
  const [step,setStep]=useState(1)
  const [address,setAddress]= useState({
    region:'',
    city:'',
    address:''
  })
  const [tel,setTel] = useState(0)
  const [selected,setSelected] = useState('')
  const [ptype,SetPtype] = useState('password')
  let {user,dispErr} = useContext(Context)
  const [loading,setLoading]= useState(false)
  useEffect(()=>{
    if(user.isAuth) navigate('user/uploads')
    else{
      setLoading(true)
      axios.get(process.env.REACT_APP_ADDRESS+'/house/city')
      .then(resp=>{
        //console.log(resp.data);
        setCities(resp.data.map(city=>{
          return city.name
        }))
      })
      .catch(e=>{
        console.log(e);
        handleNwErr(e,dispErr)
        
        
      })
      .finally(()=>setLoading(false))
    }
  },[])
  function createUser (e) {
    e.preventDefault();
    setLoading(true)
    //console.log({address,fullname:fullName});
    axios.post(process.env.REACT_APP_ADDRESS+'/user/new-user',
    {
      
      email:email,
      password:pw,
      telephone:tel,
      region:address.region,
      city:address.city,
      address:address.address,
      fullname:fullName,
      lang:localStorage.getItem('i18nextLng')
    }).then(
      res =>{
        //console.log(res);
        //alert(`account successfully created. An email has been sent to ${email}. Plese check your inbox to confirm this account `)
        navigate('/em',{state: {"email":email}})
      }
    ).catch(err =>{
      handleNwErr(err,dispErr)
       if(err.response){
        console.log(err.response.status);
        if(err.response.status===403){
          alert('email invalid')
        }
        else if(err.response.status===400){
          console.log('yes');
          let errMsg=err.response.data.error;
          //console.log(err.response); 
        
          if(errMsg.includes('telephone')){
            alert(t('phErr'))
          }
          else if(errMsg.includes('email')){
            alert(t('emErr'))
          }
          else{
            console.log(err.response.data)
            //alert(err.response.data.error)
          }
        }
        else {
          console.log(err);
        }
       }
        
      }).finally(()=>{
        setLoading(false)
      })
     
  }
  
  function updateAddress(e){
      setAddress({
        ...address,
        [e.target.name]:e.target.value
      })
  }
  function ischecked(){
    if(ptype==='password') SetPtype('text')
    else SetPtype('password')
 }
 const incStep=()=> setStep(step+1)
 
  if(step===1) {
  return <SignupPartOne action={incStep}/>
  }
  return (
    <div id='authPage' style={{overflow:'hidden'}}>
   
    <MDBContainer className='auth ' style={{ minHeight: '100vh',paddingTop: '6rem' }} fluid>
    <Loading show={loading}/>
      <MDBRow className='mx-auto text-capitalize' style={{width:'100%'}}>
        <MDBCol md="9" lg='6' className='mx-auto' xs='11' sm='8'>
        <h2 className="text-light text-center">{t('signup.p2')}</h2>
       
          <MDBCard style={{opacity:'0.7'}} >
            <MDBCardBody>
              <form onSubmit={createUser}>
                {/*<p className="h4 text-center py-4">{t("sign up")}</p>*/}
               
                <div className="blue-text inl">
                <MDBRow>
                  <MDBCol xs='10' md='10' className='mx-auto'>
                  <Form.Label>{ t("name")}:</Form.Label>
                  <Form.Control
                    label={ t("name")}
                    icon="user"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    value={fullName}
                    required
                    onChange={(e)=>{
                      
                     setFullName(e.target.value)
                    }}
                  />
                  </MDBCol>
                 
                </MDBRow>
                
               <MDBRow className="mt-2">
                 <MDBCol  xs='10' md='10'  className='mx-auto'>
                 
                 <Form.Label className='fw-normal me-2'>{ t("uploads.city")}</Form.Label>
                  <Form.Select
                      //label={ t("uploads.details.addr")+"(e.g Rail Market,Bonaberi)" }                      
                      required
                      name='city'
                      onChange={(e)=>{
                        
                         updateAddress(e)
                        }}
                      className='text-capitalize'
                    >
                    <option value=''>{ t("uploads.city")}</option>
                    {cities.length>0 && cities.map((city,index)=>{
                      return(
                        <option key={index} value={city}>{city}</option>
                      )
                    })}
                    </Form.Select>
                 </MDBCol>
                 
                 
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>

                 <Form.Label>{ t("email")}:</Form.Label>
                    <Form.Control
                      label={t("email")}
                      icon="envelope"
                      group
                      type="email"
                      validate
                      
                      value={email}
                      required
                      onChange={(e)=>{                    
                       setEmail(e.target.value)
                      }}
                      pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                    />
                 </MDBCol>
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>
                 <Form.Label>{t("signup.telephone")}:</Form.Label>
                  <Form.Control
                      //label={t("signup.telephone")}
                      //icon="phone"
                      group
                      type="tel"
                      //validate
                      placeholder='+237 614586522'
                    
                      //success="right"
                      //value={tel}
                      required
                      onChange={(e)=>{
                        setTel(e.target.value)
                      }}
                      //pattern="^(2[0-9]{8}|3[0-9]{8}|65[0-9]{7}|68[0-9]{7}|66[0-9]{7}|67[0-9]{7}|69[0-9]{7})$"
                    
                    />
                 </MDBCol>
               </MDBRow>
               <MDBRow>
                 <MDBCol  xs='10' md='10' className='mx-auto'>
                 <Form.Label>{ t("passw")}:</Form.Label>          
                    <Form.Control
                        label={ t("passw")}
                        icon="lock"
                        group
                        type={ptype}
                        validate
                        value={pw}
                        required
                        onChange={(e)=>{
                          setPw(e.target.value)
                        }}
                        pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"
                        title={t('pserr')}
                        
                      />
                      
                   
                 </MDBCol>
                 <div className="custom-control custom-checkbox ms-3 show-password float-right mt-4">
                <input type="checkbox" className="custom-control-input " id="check" onChange={ischecked}/>
                <label className ="custom-control-label fs-6" htmlFor="check">{t('showpass')}</label>
            </div>
                 {/*ptype==='password' &&( <i className="fas fa-eye glyph eye" onClick={ischecked}></i>)}
                 {ptype==='text' && ( <i className="fas fa-eye-slash glyph eye" onClick={ischecked}></i>)*/}
                
               </MDBRow>
               
                  
                </div>
                <MDBCol md="7" xs='9' className="mb-3">
            <div className="custom-control custom-checkbox pl-3">
              <input
                className="custom-control-input"
                type="checkbox"
                value=""
                id="invalidCheck"
                required
                title='you must agree to the terms'
              />
              <label className="custom-control-label text-capitalize" htmlFor="invalidCheck">
             {t("aggr")}   <NavLink to='/terms'>{t("terms_use")}</NavLink>
              </label>
            
            </div>
          </MDBCol>
                <div className="text-center py-4 mt-3">
                  <MDBBtn color="blue" type="submit">
                    {t("sign up")}
                  </MDBBtn>
                </div>
              </form>
              <p className="text-center"><Link to='/login'>{t("lg.acc")}</Link></p>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>        
      </MDBRow>
  </MDBContainer>
  </div>
  );
};

export default FormPage;


