import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Button, Card, Carousel, Col, ListGroup, Row } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { FaCheck } from 'react-icons/fa'
import { useLocation, useNavigate, useSearchParams } from 'react-router-dom'
import { Context } from '../../Context'
import { handleNwErr } from '../../utils/handleNetworkErr'

import Loading from '../loading'

export default function HouseDetails() {
    //const location =useLocation()
    const navigate=useNavigate()
    const {t}=useTranslation()
    const [house,setHouse]=useState()
    const [facilities,setFacilities]=useState( [])
    const [rules,setRules]=useState([])
    const {dispErr}=useContext(Context)
    const [loading,setLoading]=useState(false)
    const [param,setP]=useSearchParams()
    const [id,setId]=useState(param.get('id'));
    const [showAlert,setShowAlert]=useState(false)
    useEffect(()=>{
        setId(param.get('id'))
       // console.log(id);
        if(!id){
            navigate('/guest-houses')
        }
        else{
            //console.log(location.state.id);
            setLoading(true);
            axios.get(process.env.REACT_APP_ADDRESS+'/guests/'+id,)
            .then(resp=>{
                console.log(resp.data)
                if(!resp.data.id){
                    navigate('/guest-houses')
                }
                else{
                    setHouse(resp.data)
                    setFacilities(resp.data.facilities.trim().split(','))
                    setRules(resp.data.rules.trim().split(','))
                }
                
            })
            .catch(e=>{
                console.log(e);
                
                handleNwErr(e,dispErr)
            })
            .finally(()=>setLoading(false))
        }
       // console.log(location.state);
      
    },[])
  return (
    <div className='mh-100 mt-2'>
    <Loading show={loading}/>
    {house!==undefined && (
        <Row className='mx-1'>
            <Col lg={7} xs={12} className='mx-auto'>
                <Card>
                <Col xs={12} lg={10} className='text-center mx-auto' id='img-holder'>
                <Carousel>
                    {house.image!==null && house.image.length>1 &&(
                        <Carousel.Item>
                            <img
                            className="d-block  house-details-img"
                            src={house.image}
                            alt="images"
                        
                            />
                        </Carousel.Item> 
                    )}
                    { house.other_images.trim().split(' ').map(
                        (img,i)=>{
                            return(
                                <Carousel.Item>
                            <img
                            className="d-block  house-details-img"
                            src={img}
                            alt="images"
                            
                            />
                        </Carousel.Item>     
                            )
                        }
                    )}                                        
                </Carousel>
                </Col>
                
                    <Button className='d-block mx-auto' onClick={()=>navigate('/book',{state:id})}>{t('house dtails.book now')}</Button>
                    <Card.Body>
                        <Row className='mx-auto my-2'>
                            <Col xs={11} sm={10} md={4} lg={4} className='mt-1 mx-auto'>
                                <Card>
                                    <Card.Header className='fw-bold'>
                                    {t('house dtails.address')}
                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text>{house.city}</Card.Text>
                                        <Card.Text>{house.location}</Card.Text>
                                    </Card.Body>
                                </Card>
                            </Col>
                            <Col xs={11} sm={10} md={4} className='mt-1 mx-auto'>
                                <Card>
                                    <Card.Header className='fw-bold'>
                                    {t('house dtails.price')}

                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text>{process.env.REACT_APP_CUR} {' '} {house.price}{' '} {t('house dtails.per nite')}</Card.Text>                                    
                                        <Card.Text></Card.Text>
                                        <Card.Text></Card.Text>
                                    </Card.Body>
                                </Card>
                            </Col>
                            <Col className='mx-auto mt-1' xs={11} sm={10} md={4} lg={4}>
                                <Card className='mx-auto'>
                                    <Card.Header className='fw-bold'>
                                    {t('house dtails.type')}

                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text>{t('htype.'+house.type.toLowerCase())}</Card.Text>
                                        <Card.Text></Card.Text>
                                        <Card.Text></Card.Text>
                                        
                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>
                        <Row>
                        {facilities.length>0 && facilities[0].length>1 &&(
                            <Col md={6} >
                        
                            <Card>
                                <Card.Header className='fw-bold text-capitalize'>
                                {t('house dtails.facilities')}
                                </Card.Header>
                                <Card.Body>
                                    {facilities.map((f,i)=>{
                                        return(
                                            
                                            <ListGroup.Item key={i}>{t('choose.facilities.'+f)}</ListGroup.Item>
                                            
                                        )
                                    })}
                                
                                </Card.Body>
                            </Card>
                            
                            </Col>
                        )}

                            
                            <Col md={6} className='mx-auto'>
                                <Card>                            
                                    <Card.Body>
                                    <ListGroup className='text-capitalize'>
                                    
                                        <ListGroup.Item>
                                        {t('choose.livrooms')}:{house.livrooms}
                                        </ListGroup.Item>
                                    
                                        <ListGroup.Item>
                                        {t('choose.toilets')}:{house.toilets}
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                        {t('choose.rooms')}:{house.rooms}
                                        </ListGroup.Item>
                                        <ListGroup.Item>
                                        {t('choose.guests')}:{house.guestnum}
                                        </ListGroup.Item>
                                    </ListGroup>
                                        
                                    </Card.Body>
                                </Card>
                            </Col>
                        </Row>  
                        <Col md={6} className='mx-auto mt-2'>
                                <Card className='mx-auto'>
                                    <Card.Header className='fw-bold'>
                                    {t('house dtails.description')}
                                    </Card.Header>
                                    <Card.Body>
                                        <Card.Text>{house.description}</Card.Text>
                                        
                                    </Card.Body>
                                </Card>
                            </Col>   
                            {rules.length>0 && rules[0].length>1 &&(
                                <Col className='mt-2 mx-auto' >
                                <Card className='mx-auto'>
                                    <Card.Header className='fw-bold'>
                                {t('uploads.rules.title')}
                                        
                                    </Card.Header>
                                    <Card.Body>
                                        <ListGroup>

                                            { rules.map((rule,i)=>{                                            
                                                return(
                                                    <ListGroup.Item key={i}>{t('uploads.rules.'+rule)}{' '}<FaCheck size={15}/></ListGroup.Item>
                                                )
                                            })}
                                        </ListGroup>                                                                        
                                    </Card.Body>
                                </Card>                    
                            </Col>
                            )}               
                    
                        
                    </Card.Body>
                        <Card.Footer>
                        <Button className='d-block mx-auto' onClick={()=>navigate('/book',{state:house.id})}>{t('house dtails.book now')}</Button>
                        </Card.Footer>
                </Card>
            </Col>
            <Col lg={5} xs={10} className='mt-5 mx-auto'>
                    <div className='text-center'>
                        <p className='fs-4 text-capitalize'>{t('by')}:</p>
                        <h4>{house.user_name}</h4>
                        <img src={house.user_pic||'/images/no-img.png'} alt='' width={300} height={260} className='border-3 rounded agent-img'/>
                        <p className='mt-2'>{house.user_desc}</p>
                    </div>
            </Col>
        </Row>
    )}
        
    </div>
  )
}
