import React from 'react';
import {withTranslation} from 'react-i18next'

import {
 
  MDBMask,
  MDBRow,
  MDBCol,
  MDBBtn,
  MDBView,
  MDBContainer,
  
  MDBAnimation,
  MDBCard
} from 'mdbreact';
import {AuthConsumer} from '../Context'
import './homepage.css';
import { createSearchParams, Link, useNavigate } from 'react-router-dom';
import Chat from './chat';
import { Button, Card, Carousel, Col, Row } from 'react-bootstrap';
//import home from '../images/home.jpg'
const cities=['kribi','douala','limbe','yaoundé','buea']
//import { Redirect } from 'react-router-dom';
const  Cities =()=>{
  const navigate=useNavigate()
  
  return(
    cities.map((city,i)=>{
      return(
        <Col xs={11} sm={11}  md={4}  id={city} className='mx-md-1 rounded mt-2 ' key={i} onClick={()=>navigate({
          pathname:'/search',
          search:createSearchParams({
            city:city
          }).toString()

          
          })}>
          <Card className='city'>
          <p className='text-white text-center text-capitalize'>{city}</p>
          
          </Card>
        </Col>
      )
    })
  )
}
const CitiesMobile=()=>{
  const navigate=useNavigate();
  const handleNav=(city)=>{
   // console.log(city);
    navigate({
      pathname:'/search',
      search:createSearchParams({
        city:city
      }).toString(),
      
    })
  }
  return (
    <>
      <div className='d-block d-sm-none mx-auto mt-4' id='cities-mobile'>
                <div className='text-center' >
                  <MDBBtn className='btn-purple text-white text-capitalize w-2 city-btn' onClick={()=>handleNav('kribi')}>Kribi</MDBBtn>
                  <MDBBtn className='btn-purple text-white text-capitalize  city-btn' onClick={()=>handleNav('douala')}>Douala</MDBBtn>
                  <MDBBtn className='btn-purple text-white text-capitalize ' onClick={()=>handleNav('yaoundé')}>Yaoundé</MDBBtn>
                  
                </div>
                <div className='text-center'>
                <MDBBtn className='btn-purple text-white text-capitalize' onClick={()=>handleNav('buea')}>Buea</MDBBtn>
                <MDBBtn className='btn-purple text-white text-capitalize ' onClick={()=>handleNav('limbe')}>Limbe</MDBBtn>

                </div>
              </div>
    </>
  )
}
class HomePage extends React.Component {
  
  
 componentDidMount(){
   
 }

  render() {
    const {t} = this.props;
    
    return (
      
          <div id='apppage'>
          
            <MDBView >
            <span id='feedback-btn' className='btn-purple text-white btn border'><Link to="/new-review" className='text-decoration-none text-white text-uppercase'>{t('feedback')}</Link> </span>
              <MDBMask className='white-text gradient' />
              <MDBContainer
              
                className=' text-center  white-text'
                id='homepage-body'
              >
                <MDBRow className='mx-auto'>
                  <MDBCol md='11' className='text-center mx-auto'>
                    <MDBAnimation type='fadeInLeft' delay='.3s'>
                    {/*<MDBBtn color='primary' type='submit' onClick={()=>{this.props.history.push('/signup')}} size='md' className="float-sm-none"  >
                        {t('sign up')}</MDBBtn>*/}
                      <h1 className='h1-responsi font-weight-bold text-center'>
                        {t('indexHeading')}
                      
                      </h1>
                      
                      <h6 className='mb-4 '>
                      {/*t('subh')*/}
                      <div className='d-flex justify-content-center subh'>
                      <p className='text-center'>{t('furnished apartments')}</p>
                      <p className='slash'> </p>
                     <p className='ms-2'>{t('desirable locations')}</p>
                     <p className='slash'></p>
                    <p className='ms-2'>{t('reasonable prices')}</p>
                      </div>
                      

                      </h6>
                  
                        <React.Fragment>
                        <Link to='/guest-houses' type='submit'  className="float-sm-none btn btn-purple btn-text"  >
                        {t('choose your home')}</Link>
                        
                        </React.Fragment>
                        
                      
                      {/*{this.state.isRedirect && (localStorage.getItem('id') && localStorage.getItem('id')!==null)?<Redirect to="/login" /> : <Redirect to="/upload" /> }*/}
                      
                    </MDBAnimation>               
                    
                  </MDBCol>
                </MDBRow>
                
              </MDBContainer>
              <div className='d-none d-md-block'>
              <Row className=' justify-content-center mt-3  ' id='touristic'>
                
                <Cities />
               
              </Row>
              </div>
              
             <CitiesMobile/>
            </MDBView>
          
            <MDBContainer>
            <MDBAnimation type='fadeIn' delay='.1s'>

           <h2 className='text-center pt-3 fs-1'>{t("whybookwithus")}</h2>
              <MDBRow className='pt-3'>
            
                <MDBCol md='4' className='text-left text-justify'>
               
                {/*<img src='images/availability.svg' alt='availability' width='90px' height='65px'/>*/}
                <h3 className='title text-left text-md-center '>{t('Availability.title')}</h3>
                
                  <p className='text-justify'>
                  {t('Availability.desc')}
                  </p>
                </MDBCol>
                <MDBCol md='4' className='text-lef'>
               
                {/*<img src='images/satisfaction.svg' alt='availability' width='90px' height='65px'/>*/}
                <h3 className='titl text-md-center text-left'>{t('whatwedo.title')}</h3>
               
                  <ul>
                    <li>{t("whatwedo.p1")}</li>
                    <li>{t("whatwedo.p2")}</li>
                   
                    <li>{t("whatwedo.p4")}</li>
                    <li>{t("whatwedo.p5")}</li>
                    <li>{t("whatwedo.p6")}</li>
                  </ul>
                </MDBCol>
                <MDBCol md='4' xs='10' className='text-left'>           
                <h3 className='title text-left text-md-center'>{t('testimonials.title')} </h3>
            
                  <div>
                  <p className='fs-6 lh-sm text-justify'><b>Patrick</b>,<img alt='' src='images/usa.png' width={40} height={20} className='mx-2'/>
                          <br/>
                          “You truly get what you pay for. No bullshit with Residence M.G but great results. Bravo!”
                        </p>
                        <p className='fs-6 lh-sm text-justify'><b>Francois</b>, <img alt='' src='images/france.png' width={40} height={20} className='mx-2'/>

                        <br/>"J’ai été très impressionné par votre équipe. Merci de votre patience et de votre disponibilité pour répondre à mes questions. J’ai vraiment aimé les repas de Mama Steph pendant mon séjour."</p>
                        <p className=' fs-6 lh-sm text-justify'><b>Ndinga Bertin</b>,<img alt='' src='images/cmr.png' width={50} height={40} className='mx-2'/>
                          <br/>
                          “Residence M.G just got a loyal customer. My weekend in Douala was one of the most enjoyable with the best onboarding experience I’ve ever had. I can’t wait to do it over again. Well done!”
                        </p>
                  </div>
                </MDBCol>
              </MDBRow>
            </MDBAnimation>
            <hr className='my-3'/>
              <MDBRow>
                <h2 className='text-center fs-1'>{t('places to visit')}</h2>
                <Col md={4} >
                  <Card>
                    <Card.Img alt='Ekom-Nkam-Waterfall' src='images/Ekom-Nkam-Waterfall-Cameroon.jpg' height={150}/>
                    <Card.Header className='text-center'><h3>{t('touristic sites.nkongsamba.title')}</h3></Card.Header>
                    <Card.Body>
                      <span>{t('location')}:<b>Nkongsamba, {t('region.desc.Littoral')}</b></span>
                      <p className='mt-1'>{t('touristic sites.nkongsamba.desc')}</p>
                    </Card.Body>
                  </Card>
                </Col>
                <Col md={4}>
                  <Card>
                    <Card.Img alt='Mount Fako' src='images/mt-fako.jpg' height={150}/>
                    <Card.Header className='text-center'><h3>{t('touristic sites.buea.title')}</h3></Card.Header>
                    <Card.Body>
                      <span>{t('location')}:<b>Buea, {t('region.desc.South West')}</b></span>
                      <p className='mt-1'>{t('touristic sites.buea.desc')}</p>
                    </Card.Body>
                  </Card>
                </Col>
                <Col>
                  <Card md={4}>
                    <Card.Img alt='Waza National Park' src='images/waza-park.jpg' height={150}/>
                    <Card.Header className='text-center'><h3>{t('touristic sites.garoua.title')}</h3></Card.Header>
                    <Card.Body>
                      <span className='text-center'>{t('location')}:<b>Garoua,  {t('region.desc.North')}</b></span>
                      <p className='mt-1'>{t('touristic sites.garoua.desc')}</p>
                    </Card.Body>
                  </Card>
                </Col>
              </MDBRow>
            </MDBContainer>
            <Chat/>
          </div>
      
    );
  }
}

export default withTranslation()(HomePage);