import axios from 'axios'
import { MDBNavLink } from 'mdbreact'
import React, { useEffect } from 'react'
import { useNavigate, useLocation } from 'react-router'
import {useTranslation} from 'react-i18next'
import { Link } from 'react-router-dom'
import { Button } from 'react-bootstrap'

const styles={
  
        position:'relative',
        top:'12em',
        height:'80vh'
        
 
}
 function AccConfirm() {
    const {t}=useTranslation()
    return (
        <div className='text-center' style={styles}>
    <div className="cover-container d-flex h-100 p-3 mx-auto flex-column">
     
     <main role="main" class="inner cover">
       <h1 className="cover-heading">{t('acm')}</h1>
       <p className="lead">{t('acmt')}</p>
       <p className="lead">
         <Link to="/login" className="btn btn-lg btn-secondary">{t('lpbtn')} </Link>
       </p>
     </main>
   </div>
        </div>
    )
}
function PasswChangeConfirm(){
    const {t}=useTranslation()
    return (
        <div className='text-center' style={styles}>
        <div className="cover-container d-flex h-100 p-3 mx-auto flex-column">
         
         <main role="main" class="inner cover">
           <h1 className="cover-heading"> {t('prmc')}</h1>
           <p className="lead">{t('pcmt')}</p>
           <p className="lead">
             <Link to="/login" className="btn btn-lg btn-secondary">{t('lpbtn')}</Link>
           </p>
         </main>
       </div>
            </div>
    )
}

function Messg4EmailConf(){
    const loc = useLocation()
    const [email,setEmail] = React.useState('')
    
    const {t}=useTranslation()
    const navigate = useNavigate()
    useEffect(()=>{
        if(!loc.state){
            navigate(-1)
        }
        else{
            setEmail(loc.state.email)
        }
    },[])
    
    return (
        <div className='text-center container' style={styles}>
            <div className="cover-container d-flex h-100 p-3 mx-auto flex-column">
            
            <main role="main" class="inner cover">
            <h1 className="cover-heading">{t('actnm')}</h1>
            <p className="lead"> {t('actnm1')} {email}. {t('actnm2')} </p>
            <p className="lead">
                <Button  onClick={()=>navigate('/login')} className="btn btn-lg btn-secondary">{t('lpbtn')}</Button>
            </p>
            </main>
        </div>
                </div>
    )
}

function Messg4PassConf(){
    const loc = useLocation()
    
    const [mail,setMail]= React.useState('')
    const {t}=useTranslation()
    const navigate = useNavigate()

    useEffect(()=>{
       
        if(!loc.state){
            navigate(-1)
        }
        else{
            setMail(loc.state.email)
        }
    },[])
    return (
        <div className='text-center' style={styles}>
            <div className="cover-container d-flex h-100 p-3 mx-auto flex-column">
            
            <main role="main" class="inner cover">
            <h1 className="cover-heading">{t('prm')}</h1>
            <p className="lead">{t('actnm1')} {mail} {t('prmt')}</p>
            <p className="lead">
                <Link to="/login" className="btn btn-lg btn-secondary">{t('lpbtn')}</Link>
            </p>
            </main>
        </div>
                </div>
    )


}

