import React,{useState,useEffect} from 'react'

import {useTranslation} from 'react-i18next';

import ReactPaginate from 'react-paginate'

import { Button, Row } from 'react-bootstrap';


import House from './house';

export function SetHouses({houses,t}){

    return (
        <Row>
            {
                 
                houses.map((h,i)=>{
       
        return(
           <House h={h} t={t} key={i}/>
        )
   
      })
            }
        </Row>
    )
   }
export default function HousesDisp({houses,hpp,showCity}) {
  
  const [currentItems, setCurrentItems] = useState([]);
  const [pageCount, setPageCount] = useState(0);
  const [h,setH]=useState(houses)
  const [empyfilter,setEf]=useState('')
  const [cityFilter,setCF]=useState('')
  const [filteredHouses,setFilteredHouses]=useState(houses)
 
  const [itemOffset, setItemOffset] = useState(0);
  function filterByType(val) {
   // console.log(val);

    if(val){
        const f= filteredHouses.filter(house=>
            {
                return house.type.toLowerCase()===val.toLowerCase() 
            })
        setH(f)
        
    }
   
  }
  function filterByCity(val) {
       
  //  console.log(houses.length);
         if(val){
          setEf('')
           let f= houses.filter(house=>
             {
                   return house.city.toLowerCase()===val.toLowerCase() 
               })
               setH(f)
               setFilteredHouses(f)
         // console.log(f);
       }
       
   }
  useEffect(() => {
    // Fetch items from another resources.
    //setH(houses)
    const endOffset = itemOffset + hpp;
    console.log(`Loading items from ${itemOffset} to ${endOffset}`);
    setCurrentItems(h.slice(itemOffset, endOffset));
    setPageCount(Math.ceil(h.length / hpp));
  }, [itemOffset, hpp,h]);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = event.selected * hpp % h.length;
    console.log(`User requested page number ${event.selected}, which is offset ${newOffset}`);
    setItemOffset(newOffset);
  };
    const { t } = useTranslation();
   
    return (
        <div  >
         <div className="p-2 text-center mb-3 ">
         <span>{t('filter')}:</span>
         {
          //filters for city and type of house
          showCity===true &&(
           <select  onChange={(e)=>{
                    filterByCity(e.target.value);
                    setCF(e.target.value)
                    
                    }} className='mx-2 filter ' value={cityFilter}>
                    <option value=''>{t('uploads.city')}</option>
                    <option value="yaoundé">Yaoundé</option>
                    <option value="douala">Douala</option>
                    <option value="kribi">Kribi</option>
                    <option value="buea">Buea</option>
                    <option value="limbe">Limbe</option>
                   

                    
                </select>
          )
         }
        
        <select onChange={(e)=>{
                    filterByType(e.target.value)
                    setEf(e.target.value)
                }} value={empyfilter} className='filter'>
                    <option value={''}>{t('uploads.type')}</option>
                    <option value="apartment">{t('htype.apartment')}</option>
                    <option value="studio" selected>{t('htype.studio')}</option>
                    <option value="room">{t('htype.room')}</option>
                    <option value="duplex/villa">{t('htype.duplex/villa')}</option>
                    
                </select>
                <Button onClick={()=>{
                  setCF('')
                  setEf('')
                  setH(houses)
                  }}>{t('clearFilter')}</Button>
         </div>
       
         <SetHouses houses={currentItems} t={t}/> 
          <ReactPaginate
            nextLabel="next >"
            onPageChange={handlePageClick}
            pageRangeDisplayed={3}
            marginPagesDisplayed={2}
            pageCount={pageCount}
            previousLabel="< previous"
            pageClassName="page-item"
            pageLinkClassName="page-link"
            previousClassName="page-item"
            previousLinkClassName="page-link"
            nextClassName="page-item"
            nextLinkClassName="page-link"
            breakLabel="..."
            breakClassName="page-item"
            breakLinkClassName="page-link"
            containerClassName="pagination"
            activeClassName="active"
            renderOnZeroPageCount={null}
          />                         
        </div>
    )
}
