import axios from 'axios';
import { MDBBtn, MDBCol, MDBInput, MDBModal, MDBModalBody, MDBModalFooter, MDBModalHeader } from 'mdbreact';
import React, { useContext, useEffect, useState } from 'react'
import { Form } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import {Link, useLocation, useNavigate } from 'react-router-dom'
import { Context } from '../../Context';
import { handleNwErr } from '../../utils/handleNetworkErr';
import Loading from '../loading';
import { countriesData} from '../../utils/countries';

export default function MakeReservation() {
    let location=useLocation()
    let navigate=useNavigate()
    const {t}=useTranslation()
    const {dispErr}=useContext(Context)
    let date=new Date();
    let month=date.getMonth()+1;
    let today=date.getFullYear()+'-'+0+month+'-'+date.getDate()

   // console.log(today);
    useEffect(()=>{
        //console.log(location.state);
        if(!location.state){
            navigate(-1)
        }
        //setCountries(countries)
        /*setloading(true)
        /*axios.get("https://restcountries.com/v2/all")
        .then(resp=>{
            let {data}=resp
            
           setCountries(data.map((country)=>{
                return country.name
            }))
        })
        .catch(e=>{
            if(!e.response){
                handleNwErr(e,dispErr)
            }
        })
        .finally(()=>setloading(false))*/
    },[])
    const [countries,setCountries]=useState(countriesData)
   
    const [loading,setloading]=useState(false)
    const [response,setRestponse]=useState('')
    const [showAlert,setShowAlert]=useState(false)
    //const [fullname,setFullName]=useState('')
   
    const [newReserve,setNewReserve]=useState({
        checkin:'',
        checkout:'',
        time:undefined,
        telephone:'',
        options:'',
        houseId:location.state,
        date:new Date(),
        fullname:'',
        email:'',
        persons:'',
        country:''
    })
    const editNewR=(e)=>{
        let {name,value}=e.target
        setNewReserve({
            ...newReserve,
            [name]:value
        })
    }
    const saveReservation=(e)=>{
        e.preventDefault();
       // setloading(true)
       // console.log(newReserve);
        axios.post(process.env.REACT_APP_ADDRESS+'/guests/book',newReserve)
        .then(data=>{
            setRestponse(t('reserveP.success'))
            setShowAlert(true)
        })
        .catch(e=>{
            console.log(e);
            if(e.response){
                console.log(e.response.data);
            }
            if(!e.response){
                handleNwErr(e,dispErr)
            }
            setRestponse(t('reserveP.err'))
            setShowAlert(true)
        })
        .finally(()=>setloading(false))
    }
    const toggleModal=()=>{setShowAlert(!showAlert)}
  return (
    <div className='mh-100' id='reservation'>
    <Loading show={loading}/>
        <h3 className='text-center' style={{color:'white'}}> {t('reserveP.mheading')} {location.state}</h3>
        <p className='text-center fw-bolder text-white'>{t('reserveP.mheading')} {newReserve.houseId}
    </p>
    <MDBModal isOpen={showAlert} toggle={toggleModal} >
    <MDBModalHeader toggle={toggleModal}></MDBModalHeader>
          <MDBModalBody>
           <p className='text-center'>{response}</p>
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={()=>navigate('/guest-houses')}>{t('close')}</MDBBtn>
          </MDBModalFooter>
    </MDBModal>
    <MDBCol sm='12' md='8' lg='6' className='mx-auto'>
        <form className='d-block text-capitalize' onSubmit={saveReservation}>
        <p className="h4 text-center mb-4 text-capitalize">{t('reserveP.formh')} </p>
        <div className='mx-auto'>
        
            <div className='form-group col-md-6 mx-auto'>
            <label htmlFor='fullname' >{t('reserveP.name')}<span className='text-danger'>*</span></label>
                <input
                className='form-control'
                type='text'
                id='fullname'
               name='fullname'
                onChange={editNewR}

                required
                />
            </div>
            <div className='form-group col-md-6 mx-auto'>
            <label htmlFor='email' >Email<span className='text-danger'>*</span></label>
                <input
                className='form-control'
                type='email'
                id='email'
               name='email'
                onChange={editNewR}

                required
                />
            </div>
        </div>
        <div className=' mx-auto'>
            <div className='form-group col-md-6  mx-auto'>
                <label htmlFor='checkIn' >{t('reserveP.checkin')}<span className='text-danger'>*</span></label>
                <input
                className='form-control'
                type='date'
                    id='checkIn'
                    name='checkin'
                    onChange={editNewR}
                    min={today}
                required
                />
            </div>
            <div className=' col-md-6 form-group  mx-auto'>
                <label htmlFor='checkOut' >{t('reserveP.checkout')}<span className='text-danger'>*</span></label>
                <input
                className='form-control'
                type='date'
                id='checkOut'
                name='checkout'
                min={newReserve.checkin.length>0?newReserve.checkin:today}
                onChange={editNewR}

                required
                />
            </div>
            
        </div>
        <div className='form-group col-md-6 mx-auto'>
        <label>{t('reserveP.time')}<span className='text-danger'>*</span></label>
        <input type='time' className='form-control' name='time' required onChange={editNewR}/>
         </div>
        
   <div className=''>
   <div className='form-group col-md-6  mx-auto'>
        <label>{t('reserveP.country')}</label>
        <Form.Select name="country" onChange={editNewR} required>
            <option value={''}></option>
            {countries.map((c,i)=>{
                return(
                    <option key={i} value={c}>{c}</option>
                )
            })}
        </Form.Select>
    </div>
   
    <div className='form-group col-md-6 mx-auto'>
        <label>{t('reserveP.number of guests')}<span className='text-danger'>*</span></label>
        <input type='number' min='1' placeholder='' name='persons' className='form-control' required onChange={editNewR}/>
    </div>
   </div>
   <div className=''>
   <div className='form-group col-md-6 mx-auto'>
        <label>{t('reserveP.telephone')}{t('reserveP.whatsapp number')}<span className='text-danger'>*</span></label>
        <input type='tel' min='9' placeholder={t('reserveP.whatsapp number')} name='telephone' className='form-control' required onChange={editNewR}/>
    </div>
    <div className='form-group col-md-6  mx-auto'>
        <label>{t('reserveP.other')} </label>
        <textarea className='form-control'  rows="3" name="options" onChange={editNewR} />
    </div>
   </div>
   <div className="custom-control custom-checkbox text-md-center mx-2 t">
              <input
                className="custom-control-input"
                type="checkbox"                
                id="invalidCheck"
                required
              />
              <label className="custom-control-label text-capitalize" htmlFor="invalidCheck">
             {t("aggr")}   <Link to='/terms'>{t("terms_use")}</Link>
              </label>
            
            </div>
   {/*<span  className='btn-purple text-white btn border'><Link to="/terms" className='text-decoration-none text-white text-uppercase'>{t('terms_use')}</Link> </span>
    <p className='text-center'>NB:{t('reserveP.telephone')}</p>*/}
    <MDBBtn type='submit' className='d-block mx-auto'>{t('reserveP.send')}</MDBBtn>
        </form>
        
    </MDBCol>
  
    </div>
  )
}
