import axios from 'axios'
import { MDBBtn, MDBCard, MDBCardBody, MDBCol, MDBContainer, MDBIcon, MDBRow } from 'mdbreact'
import React, { useContext, useEffect, useRef, useState } from 'react'
import { Form, FormControl } from 'react-bootstrap'
import {useTranslation} from 'react-i18next'
import {  useNavigate} from 'react-router'
import {Context} from '../../Context'
import {regions,cr,apartments,ar,er,fnr,nr,nwr,lr,wr,swr,sr} from '../../data'
import { handleNwErr } from '../../utils/handleNetworkErr'
import Loading from '../loading'
import UpdatePassword from './passwordUpdate'


const setFormData=(user)=>{
  let formD=new FormData()
  //if(user.image!=null){
    formD.append('image',user.imgDisp)
  //}
  formD.append('fullname',user.fullname)
  formD.append('address',user.address)
  formD.append('email',user.email)
  formD.append('city',user.city)
  formD.append('telephone',user.telephone)
  formD.append('description',user.description)
  return formD
}
export default function UpdateProfile() {
    const navigate = useNavigate()
    const {t}= useTranslation()
    const inputElement=useRef()
    let {user,session_exp,dispErr}= useContext(Context)
    //console.log(user)
    const [userD,setUserD]=useState({
      fullname:'', 
      region:'', 
      city:'',
      address:'',
       email:'',
        telephone:'', 
        description:"",
        image:null,
        imgDisp:null
  })
    const [cities,setCities]= useState('')
    const [loading,setLoading]=useState(false)
    const setFocus=() =>{
      inputElement.current.click()
    }
   useEffect(()=>{

    if(user.isAuth===false){
        navigate('/login')
    } else{
      setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/user/',{id:parseInt(user.ui)},{withCredentials:true}).then(
            res=>{
                console.log(res.data.description===null);
                setUserD({
                    fullname:res.data.fullname,                                      
                    city:res.data.city,          
                    email:res.data.email,
                   telephone:res.data.telephone,
                  image:res.data.image,
                  description:res.data.description,
                  imgDisp:null

                   
                })
                //console.log(user);
            }
        )
        .catch(e=>{
          console.log(e);
          handleNwErr(e,dispErr)
          if(e.response){
            if(e.response.data.auth){
              session_exp()
            }

        }
        })
        .finally(()=>setLoading(false));
        setLoading(true)
      axios.get(process.env.REACT_APP_ADDRESS+'/house/city')
      .then(resp=>{
        //console.log(resp.data);
        setCities(resp.data.map(city=>{
          return city.name
        }))
      })
      .catch(e=>{
        console.log(e);
      })
      .finally(()=>setLoading(false))
    
    }        
    },[])
    function changeHandler(e){
      setUserD({
        ...userD,
        [e.target.name]:e.target.value
      })
    }
    const dispImg=(e)=>{
      //changeHandler(e)
      
        if (e.target.files && e.target.files[0]) {
          let reader = new FileReader();
          reader.onload = (evt) => {
            setUserD({
              ...userD,
              image: evt.target.result,
              imgDisp:e.target.files[0]
            
          })
          };
          reader.readAsDataURL(e.target.files[0]);
         
     
      }
    }
    function submit(e){
      e.preventDefault()
     
      let fd=setFormData(userD)
      setLoading(true)
      axios.put(process.env.REACT_APP_ADDRESS+'/user/edit',
      fd,
      {withCredentials:true}).then(
        res=>{
          alert(res.data)
          if(user.isAdmin===1) navigate('/admin/settings')
          else navigate('/user/settings')
        }
      ).catch(
        e=>{
          console.log(e);
          handleNwErr(e,dispErr)
          if(e.response){
            if(e.response.auth){
                navigate('/login',{state:{msg:'session expired'}})
            }
        }
        }
      )
      .finally(()=>setLoading(false))
    }
    return (
        <div style={{margin:'auto',overflow:'scroll',maxHeight:'auto'}} className='mh-100'>
        <Loading show={loading}/>
           <MDBContainer>
      <MDBRow>
        <MDBCol sm="11" lg='6' className='mx-auto my-md-2'>
        
          
          <MDBCard style={{backgroundColor:" #bb8fce "}} className='mx-auto'>
            <MDBCardBody>
              <Form onSubmit={submit}>
                <h4 className="h4 text-center py-4">{t('edit profile')}</h4>
                <p>{t("settings.img_desc")}</p>
                <div className='text-center'>
                <input className='d-none' type='file' accept='image/png,image/jpeg' ref={inputElement} name='image' onChange={dispImg} />
                <img src={userD.image==null?"/images/no-img.png":userD.image} alt='' height={85} width={110} onClick={setFocus} className='border rounded'/>
                <p onClick={setFocus}><u>{t('settings.change_image')}</u></p>
                
                </div>
                <div className='form-group'>
                  <label className='text-left'>{t('settings.description')}</label>
                  <FormControl as="textarea" name='description' onChange={changeHandler} required placeholder={t('settings.placeholder')} rows={4} value={userD.description}/>
                </div>
       
                <label
                  htmlFor="fullname"
                  className="t font-weight-light text-capitalize"
                >
                  {t("fulln")}
                </label>
                <input
                  type="text"
                  id="fullname"
                  name="fullname"
                  className="form-control"
                  defaultValue={userD.fullname}
                  required
                  onChange={changeHandler}
                />
                
                <br />
                <label
                  htmlFor="telephone"
                  className="t font-weight-light text-capitalize"
                >
                 {t("your")+ " " + t("telephone")}
                </label>
                <input
                  type="text"
                  id="telephone"
                  name="telephone"
                  defaultValue={userD.telephone}
                  onChange={changeHandler}
                  className="form-control"
                  required
                />
                <br />
                <label
                  htmlFor="email"
                  className=" font-weight-light text-capitalize"
                >
                 {t("your")+ " " + t("email")}
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  className="form-control"
                  defaultValue={userD.email}
                  required
                  onChange={changeHandler}
                /><br/>
                
                <br />
                <label style={{fontWeight:'bolder',textTransform:"capitalize", color:"black !important"}}
                  htmlFor="city"
                  className="t font-weight-light text-capitalize"
                >
               {t('choose.ct')}
                </label>
                <select
                  
                  id="city"
                  name="city"
                  className="form-control browser-default custom-select text-capitalize"
                  onChange={changeHandler}
                  required
                >
                  <option value={''}></option>
                  {cities.length>0 && cities.map((city,index)=>{
                      return(
                        <option key={index} value={city} selected={userD.city===city}>{city}</option>
                      )
                    })}
                </select>
                <br />
               <br/>
               {/* <label
                  htmlFor="address"
                  className="t font-weight-light text-capitalize"
                >
                 {t("your")+ " "+ t("uploads.details.addr")}
                </label>
                <input
                  type="text"
                  id="address"
                  name="address"
                  className="form-control"
                  defaultValue={userD.address}
                  onChange={changeHandler}
                  required
                />*/}
                <div className="text-center py-4 mt-3">
                  <MDBBtn className="btn btn-outline-purple" type="submit">
                    {t('update')}
                    <MDBIcon far icon="paper-plane" className="ml-2" />
                  </MDBBtn>
                </div>
              </Form>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>
      </MDBRow>
      <UpdatePassword/>
    </MDBContainer> 
        </div>
    )
}
