import React, { useContext, useEffect } from 'react'
import { Route, Routes, useNavigate } from 'react-router-dom'
import AdminView from './adminView';
import Users from './users';
import Admins from './admins';
import NewAdmin from './newAdmin';
import Reservations from './reservations';
import {Context} from '../../Context'
import { AdminSidebar } from '../../components/sidebars';
import Reviews from './review';
import Default from '../default';
import NewPost from './newPost';
import AllPosts from './allPosts';
import Post from './post';
import FilesUploadComponent from '../houses/upload';
import Settings from '../user/settings';
import UpdateProfile from '../user/editProfile';
import UpdatePassword from '../user/passwordUpdate';
import EditHouse from '../houses/editHouse';

//<Route path='/admin/houses/all' component={AdminView}/>
export default function AdminIndex() {
  const navigate= useNavigate()
  const {user}=useContext(Context)
    useEffect(()=>{
        if(user.isAuth!==true){
          navigate('/login')
        }
        else{
          if(user.isAdmin!==1){
            navigate(-1)
          }
        }

    },[])
  return (
      <div style={{display:"flex",  height:"100vh", overflowY:"hidden",overflowX:"scroll"}}>
        <AdminSidebar />
     <div style={{minHeight:'100vh',width:'100%',overflow:'scroll'}}>
      <Routes>
          <Route exact path='/' element={<AdminView/>}>
          
          </Route>
        
          <Route path='/all-users' element={<Users/>}/>
          <Route path='/upload' element={<FilesUploadComponent/>}/>
          <Route path='/all-admins' element={<Admins/>}/>
          {/*<Route path='/new-admin' element={<NewAdmin/>}/>*/}
          <Route path='/bookings' element={<Reservations/>}/>
          <Route path='/reviews' element={<Reviews/>}/>
          <Route path='/new-post' element={<NewPost/>}/>
          <Route path='/all-posts' element={<AllPosts/>}/>
          <Route path='/post' element={<Post/>}/>
          <Route path='/edit-house' element={<EditHouse/>}/>
          <Route path='/settings' element={<UpdateProfile/>}/>
          {/*<Route path='/settings/update' element={<UpdateProfile/>}/>
          <Route path='/settings/password-update' element={<UpdatePassword/>}/>*/}
          <Route path='*' element={<Default/>}/>
      </Routes>
    </div>
    </div>
  )
}
