import React from 'react'
import { Button, Col, Form, Row } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';

export default function Facilities({currentStep,next,prev,handleChange,lastStep,setFacilities,state}) {
const facilities=
[
    'air conditioner',
    'hot water',
    'wifi',
   'constant water supply',
    'standby generator',
    'Washing machine',
    'swimming pool',
    'flat-screen TV',
    'gym',
   'bathtub',
    'decoder',
    'netFlix',
    'fire alarm',
    'parking space',
   'video surveillance camera',
   'security guard'
]
    const {t}=useTranslation()
    if(currentStep!==3){
        return null;
    }
  return (
    <div className='px-2 py-4'>
        <Form onSubmit={next}>
        <h4 className='text-center'>{t('choose.select facilities')}</h4>
            <Row >
                {facilities.map((facility,i)=>{
                    return(
                        <Col key={i} md={4} lg={3}>
                        <Form.Check 
                            value={facility}
                            type='checkbox'
                            name={facility}
                            label={t('choose.facilities.'+facility)}
                            onChange={setFacilities}
                            defaultChecked={state.facilities.includes(facility)}
                        />
                        </Col>
                    )
                })}
                </Row>
                <Button onClick={prev} disabled={currentStep<=1?true:false}>{t("choose.prev")}</Button>
              <Button  disabled={currentStep>=lastStep?true:false} type='submit'>{t("choose.next")}</Button>
        </Form>
    </div>
  )
}
