import React from 'react';
import { MDBContainer, MDBNavbar, MDBNavbarBrand, MDBNavbarNav, MDBNavbarToggler, MDBCollapse, MDBNavItem, MDBNavLink, MDBIcon, MDBBtn, MDBDropdown, MDBDropdownToggle, MDBDropdownItem, MDBDropdownMenu } from 'mdbreact';

//import './navigation.css'
import {AuthConsumer, Context} from '../Context'
import Cookies from 'js-cookie'
import {withTranslation} from 'react-i18next'
import i18next from 'i18next'
import axios from 'axios';
import { Link } from 'react-router-dom';
//import {withCookies} from 'react-cookie'

class Navigation extends React.Component {
  static contextType= Context
  constructor(props) {
      super(props);
      this.state = {
          collapse: false,
          hasId:false,
          hasNum:false,
          selected:localStorage.getItem('i18nextLng')
      
      };
      this.onClick = this.onClick.bind(this);
      this.refreshPage = this.refreshPage.bind(this)
      this.handleClick= this.handleClick.bind(this)
      this.collapseNav=this.collapseNav.bind(this)
  }

  onClick() {
    this.setState({
        collapse: !this.state.collapse,
      });
  }
  refreshPage() {

    /*localStorage.removeItem('rmg_nu')
    localStorage.removeItem('rmg_iu')
    localStorage.removeItem('rmg_ru')*/
    //this.props.history.push('/')
    axios.post(process.env.REACT_APP_ADDRESS+"/logout").then(
      res=>console.log(res.data)
    ).catch(e=>console.log(e))
    /*Cookies.remove('rmg_iu')
    Cookies.remove('rmg_nu')
    Cookies.remove('rmg_ru')
    Cookies.remove('rmg_tu')*/
    //window.location.reload(false);
   this.context.logout();
    
  }
  handleClick(e){
   
    i18next.changeLanguage(e.target.value)
    this.setState({
      selected:e.target.value,
      collapse:false
    })
   
  }
  collapseNav(){
    this.setState({
      collapse:false
    })
  }
  render() {
    const {t} = this.props;
    let user=this.context
  
    return(
      <AuthConsumer>{
        value=>{
          return (
            <MDBContainer className='mainmenu ' fluid  >
          <header>
        
          
            <MDBNavbar  light expand="lg" scrolling className='menubar' >
            
            <MDBContainer>
            <MDBNavbarBrand className='text-left'>
                    <Link to='/'><img src='../images/logo.png' alt='LOGO' width='100px' height='75px'/></Link>
                </MDBNavbarBrand>
           
                <MDBNavbarToggler onClick={ this.onClick } />
                <MDBCollapse isOpen = { this.state.collapse } navbar>
                  <MDBNavbarNav className=' navmenu font-weight-bold text-justify text-center' >
                  <MDBNavItem onClick={this.collapseNav}>
                        <Link to="/">{t('home')}</Link>
                    </MDBNavItem>
                    <MDBNavItem onClick={this.collapseNav}>
                        <Link to="/about">{t('about.about us.title')}</Link>
                    </MDBNavItem>
                    <MDBNavItem onClick={this.collapseNav} className=''>
                        <Link to="/guest-houses">{t('apartments')}</Link>
                    </MDBNavItem>
                    {
                      user.user.isAuth===false &&(
                        <MDBNavItem onClick={this.collapseNav} className='border mx-2 rounded border-dark'>
                        <Link to="/signup" className='text-center'>{t('list_property')}</Link>
                    </MDBNavItem>
                      )
                    }
                   
                    <MDBNavItem onClick={this.collapseNav}>
                        <Link to="/news">{t('news.title')}</Link>
                    </MDBNavItem>
                    <MDBNavItem onClick={this.collapseNav}>
                        <Link to="/contact">{t('contact')}</Link>
                    </MDBNavItem>
                    {
                      user.user.isAuth===false ?(
                        <MDBNavItem onClick={this.collapseNav}>
                          <Link to="/login" className='lasta'>{t('log in')}</Link>
                        </MDBNavItem>  
                      ):(
                        <MDBNavItem onClick={this.collapseNav}>
                          <Link to={parseInt(user.user.isAdmin)===1?"/admin":"/user"} className='lasta'>{t('account')}</Link>
                        </MDBNavItem>
                      )}
                                    
                  
                      <MDBNavItem>
                      <select onChange={this.handleClick} value={this.state.selected}
                    
                      style={{marginTop:'15px',marginLeft:'12px'}}>
                        <option  value='en'>English</option>
                        <option  value='fr'>Français</option>
                      </select>
                      </MDBNavItem>
                      
                   
                  </MDBNavbarNav>
                
                </MDBCollapse>
                </MDBContainer>
              </MDBNavbar>
            
          </header>
    </MDBContainer>
          )
        }
      }
           
    </AuthConsumer> 
      
     
   
    );
  }
}

export default withTranslation()(Navigation);


