import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Button, Col, Form } from 'react-bootstrap'
import ReactQuill from 'react-quill'
import 'react-quill/dist/quill.snow.css'
import { useLocation, useNavigate } from 'react-router-dom'
import DelPrompt from '../../components/delPrompt'
import { Context } from '../../Context'
import { handleNwErr } from '../../utils/handleNetworkErr'
import Loading from '../loading'
import { modules } from './newPost'

export default function Post() {
    const navigate=useNavigate()
    const location=useLocation()
    const [showModal,setShowModal]=useState(false)
    const [title,setTitle]=useState(location.state.title||'')
    const [loading,setLoading]=useState(false)
    const {dispErr}=useContext(Context)
    const [post,setPost]=useState({
        body:location.state.body||'',
        title:location.state.title||'',
        postId:location.state.id||null,
        status:location.state.status||null,
        date:location.state.date||null,
        slug:location.state.slug||''
    })
    useEffect(()=>{
        if(!location.state) {
            navigate(-1)
        }
       /*  else{
            let p=location.state
            console.log(post.status);
           setPost({
                body:p.body,
                title:p.title,
                id:p.id
            })
        }*/
       
    },[])
    const hide=()=>{setShowModal(false)}
    const showPm=()=>setShowModal(true)
    const del=()=>{
      setLoading(true)
      setShowModal(false)
      axios.post(process.env.REACT_APP_ADDRESS+'/a/del-post',
      {
        postId:post.id,
      },{withCredentials:true})
     // axios.post(process.env.REACT_APP_ADDRESS+'/houses',{id:parseInt(user.ui)},{withCredentials:true})
      .then(res=>{
        // console.log(res.data);
          navigate('/admin/all-posts')
          //console.log(houses);
      })
      .catch(err=>{
           console.log(err);
           handleNwErr(err,dispErr)
           if(err.response){
            let resp=err.response
               if(resp.data.auth){
                   navigate('/login',{state:{msg:'session expired'}})
               }
               
               else if(resp.status===500){
                alert(resp.data)
               }
           }
           console.log(err);
           
           })
           .finally(()=>setLoading(false))
  
    }

    const changeStatus=(status)=>{
      setLoading(true)
      axios.post(process.env.REACT_APP_ADDRESS+'/a/post-status',
      {
        
        postId:post.id,
        status:status
      },{withCredentials:true})
     // axios.post(process.env.REACT_APP_ADDRESS+'/houses',{id:parseInt(user.ui)},{withCredentials:true})
      .then(res=>{
         console.log(res.data);        
          setPost({
            ...post,
            status:status
          })
          //console.log(houses);
      })
      .catch(err=>{
           console.log(err);
           handleNwErr(err,dispErr)
           if(err.response){
            let resp=err.response
               if(resp.data.auth){
                   navigate('/login',{state:{msg:'session expired'}})
               }
               
               else if(resp.status===400){
                alert(resp.data)
               }
           }
           console.log(err);
           
           })
           .finally(()=>setLoading(false))
  
    }
    const edit=(e)=>{
      e.preventDefault()
      if(post.title!==title ){
        setPost({
          ...post,
          slug:post.title.trim().split(' ').join('-')+Date.now()
        })
      }
      if(post.title.trim().length>0){
        setLoading(true)
        axios.post(process.env.REACT_APP_ADDRESS+'/a/edit-post',
        post,{withCredentials:true})
        .then(res=>{
          alert('done');            
        })
        .catch(err=>{
          handleNwErr(err,dispErr)
             console.log(err);
             if(err.response){
              let resp=err.response
                 if(resp.data.auth){
                     navigate('/login',{state:{msg:'session expired'}})
                 }
                 
                 else if(resp.status===500){
                  alert(resp.data)
                 }
             }
             console.log(err);
             
             })
             .finally(()=>setLoading(false))
      }
     
    }
  return (
    <div>
    <h3 className='text-center'>View post</h3>
    <div className='text-center'>
      {parseInt(post.status)===1? 
      <Button onClick={()=>{
        changeStatus(0)
      }}>Unpublish</Button>:
      <Button onClick={()=>changeStatus(1)}>Publish</Button>}
      <Button onClick={showPm} variant='danger'>Delete Post</Button>
    </div>
    
    <Loading show={loading}/>
   
       <Col className='card mx-auto' xs={11} md={8}>
       <Form onSubmit={edit}>

       
            <Form.Control className='text-center' value={post.title} required onChange={(e)=>{
              
              setPost({
                ...post,
                
                title:e.target.value
              })
            }}/>
        <ReactQuill theme="snow" value={post.body} modules={modules} onChange={(t)=>setPost({
          ...post,
          body:t
        })}/>
        <Button type='submit' >Save Changes</Button>
        </Form>
      </Col>
      

      <DelPrompt hide={hide} showModal={showModal} question="This Article will be deleted" action={del}/>
      <br/>
     
    </div>
  )
}
