import React, { useContext, useEffect } from 'react'
import { Route, Routes, useNavigate } from 'react-router-dom'
import { UserSidebar } from '../../components/sidebars'
import { Context } from '../../Context'
import EditHouse from '../houses/editHouse'
import MyHouses from '../houses/myHouses'
import FilesUploadComponent from '../houses/upload'
import UpdateProfile from './editProfile'
import UpdatePassword from './passwordUpdate'
import Settings from './settings';
import Default from '../default'

export default function UserIndex() {
    const {user}=useContext(Context)
    const navigate=useNavigate()
    useEffect(()=>{
        if(user.isAuth!==true){
            navigate('/login')
        }
    })
  return (
    <div style={{display: 'flex',  height: '100vh', overflowX: 'scroll',overflowY:"hidden"}}>
    <UserSidebar/>
    <div style={{width:'100%',minHeight:'100vh'}} >
      <Routes>
          <Route exact path='/' element={<MyHouses/>}/>
          <Route path='/edit-house' element={<EditHouse/>}/>
          {/*<Route path='/update-profile' element={<UpdateProfile/>}/>
          <Route path='/update-pasw' element={<UpdatePassword/>}/>
          <Route path='/settings' element={<Settings/>}/>*/}
          <Route path='/settings' element={<UpdateProfile/>}/>
          <Route path='/upload' element={<FilesUploadComponent/>}/>
          <Route path='*' element={<Default/>}/>

      </Routes>
      </div>
    </div>
  )
}
