import axios from 'axios';
import React, { useContext, useState } from 'react'
import { Button, Col, Form } from 'react-bootstrap';

import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useNavigate } from 'react-router-dom';
import { Context } from '../../Context';
import { handleNwErr } from '../../utils/handleNetworkErr';
//import 'react-quill/dist/quill.bubble.css'

export const modules = {
  toolbar: [
    [{ 'header': [1, 2, 3,4,5,6,false] }],
    ['bold', 'italic', 'underline','strike', 'blockquote'],
    [{'list': 'ordered'}, {'list': 'bullet'}, {'indent': '-1'}, {'indent': '+1'}],
    ['link'],
    [{ 'color': [] }, { 'background': [] }],
    [{ 'font': [] }],
    [{ 'align': [] }],
    ['clean']
  ],
},

formats = [
  'header',
  'bold', 'italic', 'underline', 'strike', 'blockquote',
  'list', 'bullet', 'indent','font','align',
  'link', 'image'
]
export default function NewPost() {
  const [value, setValue] = useState('');
  const [title,setTitle]=useState('')
  const [slug,setSlug]=useState('')
  const [status,setStatus]=useState(0)
  const [loading,setLoading]=useState(false)
  const {dispErr}=useContext(Context)
  const navigate=useNavigate()
  const save=(e)=>{
    e.preventDefault()
    setLoading(true)
    axios.post(process.env.REACT_APP_ADDRESS+'/a/newpost',
    {
      title:title,
      slug:slug+Date.now(),
      post:value,
      status:status
    },{withCredentials:true})
    .then(res=>{
       console.log(res.data);
        navigate('/admin/all-posts')
        //console.log(houses);
    })
    .catch(err=>{
      handleNwErr(err,dispErr)
         console.log(err);
         if(err.response){
          let resp=err.response
             if(resp.data.auth){
                 navigate('/login',{state:{msg:'session expired'}})
             }
             
             else if(resp.status===400){
              alert(resp.data)
             }
         }
         console.log(err);
         
         })
         .finally(()=>setLoading(false))

  }
  const handleTitleChange=(e)=>{
    let val=e.target.value
    setTitle(val)
    let s=val.trim().split(' ').join('-')
    setSlug(s)
  }
 
  return (
    <div className='mx-2 ' id='blog-post'>
    <h6 className='text-center'>new post</h6>
    <Col  xs={11} md={8} lg={6} className='mx-auto'>
    <Form className='d-block mx-auto ' onSubmit={save}>
      <Form.Group >
        <Form.Label>Title</Form.Label>
        <Form.Control type='text' required placeholder='title here' value={title} onChange={handleTitleChange}/>
      </Form.Group>
      <Form.Group>
        <Form.Label>Slug</Form.Label>
        <Form.Control type='text' required value={slug} readOnly/>
      </Form.Group>
      <Form.Group >
      <Form.Label>Write your Article below:</Form.Label>
      <ReactQuill theme="snow" value={value} onChange={setValue}  modules={modules}/>
      </Form.Group>
      <Form.Group>
      <Form.Check            
            type='radio'
            label='Save As draft'
            name='status'
            value={0}
            required
            onChange={(e)=>setStatus(e.target.value)}
          />
           <Form.Check            
            type='radio'
            label='Publish'
            name='status'
            value={1}
            onChange={(e)=>setStatus(e.target.value)}
            required
          />
      </Form.Group>
      <Button type='submit'>Save</Button>
      <br/>
      <div>
    {/* <ReactQuill
    value={value}
    readOnly={true}
    theme={"bubble"}
  />*/}
      </div>
    </Form>
    </Col>
    
    </div>
  )
}

