import React, { useState } from 'react'
import {useTranslation} from 'react-i18next';
import { MDBContainer, MDBRow, MDBCol, MDBBtn, MDBIcon, MDBView, MDBCarouselItem, MDBCarouselInner, MDBCarousel, MDBModalBody, MDBModalHeader, MDBModal, MDBModalFooter, MDBCard  } from "mdbreact";
import { Link, useNavigate } from 'react-router-dom';
import { Button, Card, ListGroup } from 'react-bootstrap';
import { FaCheck } from 'react-icons/fa';
import DelPrompt from '../components/delPrompt';
export default function DetailsModal({images,h,closeModal,data}) {

    const { t } = useTranslation();
    const navigate = useNavigate();
    const [facilities,setFacilities]=useState( h.facilities.trim(' ').split(','))
    const [rules,setRules]=useState(h.rules.trim().split(','))
    return (
        <>
            {/*<DelPrompt showModal={showPrompt} 
               hide={()=>setShowPrompt(false)}
                question={'do you really want to delete house with id '+h.id+"?"}
                action={dropHouse(h.id)}
                />*/}
        
        
             <MDBModal isOpen={data===h.id} toggle={closeModal} size='lg'>
                      <MDBModalHeader toggle={closeModal}>
                      {t('uploads.details.details').toUpperCase()}
                     
                      </MDBModalHeader>
                      <MDBModalBody>
                      {images.length >1 && (
                        <MDBCarousel
                      activeItem={1}
                      length={images.length}
                      showControls={true}
                      showIndicators={false}
                      className="z-depth-1"
                      slide
                      >
                      <MDBCarouselInner >
                    {images.map(ims=>{
                         return ( 
                          <MDBCarouselItem itemId={images.indexOf(ims)+1} key={images.indexOf(ims)}>
                          <MDBView>
                              <MDBRow id=' ' style={{height:'55vh'}}>
                              <img src={ims} className=' ' alt='images' 
                              style={{maxHeight:'100%',margin:'auto',width:'100%',height:'85vh', maxWidth:'100%'}}/>
                              </MDBRow>
                          </MDBView>                                               
                          </MDBCarouselItem>
                        )
                     })}                                            
                      </MDBCarouselInner>                                                
                      </MDBCarousel>
                      
                      )}
                      {images.length<2 &&(
                        <MDBCol md='8' className='  mx-auto'>
                        <img src={images[0]} className=' w-100 d-block ' alt='images' style={{width:'100%',height:'40vh',margin:'auto'}} />
                        </MDBCol>
                        
                      )}
                      <br/>
                      
                      <MDBRow>
                          <MDBCol md='6' xs='12' >
                          <h3 className='fs-5'>{t('uploads.details.addr').toUpperCase()}:</h3>
                          <li className='text-capitalize'>{t('uploads.city')}: {h.city}</li>                          
                          <li className='text-capitalize'>{t('uploads.details.addr')}: {h.location}</li>
                          </MDBCol>
                          <MDBCol md='6' xs='12' >
                          <h3 className='text-capitalize fs-5'>{t('uploads.type').toUpperCase()}:</h3>
                          <li className='text-capitalize fs-5'>{t('uploads.ref')}: {h.id} </li>
                      
                          <li className='text-capitalize fs-5'> {t('htype.'+h.type.toLowerCase())}</li>
                          </MDBCol>
                          
                      </MDBRow>                     
                    <br/>
                        <MDBRow>
                        <MDBCol md='6' className='mx-auto'>
                            <Card>                            
                                <Card.Body>
                                <ListGroup className='text-capitalize'>
                                    <ListGroup.Item>
                                    {t('choose.rooms')}:{h.rooms}
                                    </ListGroup.Item>
                                    <ListGroup.Item>
                                    {t('choose.livrooms')}:{h.livrooms}
                                    </ListGroup.Item>
                                   
                                    <ListGroup.Item>
                                    {t('choose.toilets')}:{h.toilets}
                                    </ListGroup.Item>
                                    
                                    <ListGroup.Item>
                                    {t('choose.guests')}:{h.guestnum}
                                    </ListGroup.Item>
                                </ListGroup>
                                    
                                </Card.Body>
                            </Card>
                        </MDBCol>
                          <MDBCol md='6' xs='12'>
                          <h3 className='fs-5'>{t('uploads.details.description').toUpperCase()}:</h3>
                          {(h.description !==null) && (
                          <li>{h.description}</li>                          
                      )}
                          </MDBCol>
                        </MDBRow>
                        <br/>
                      <MDBRow className='mx-auto'>    
                          <MDBCol md='6' xs='12'>
                          <h3 className='fs-5'>{t('uploads.details.price').toUpperCase()}:</h3>
                          <li ><b> {process.env.REACT_APP_CUR} {h.price} </b></li>
                          </MDBCol> 
                          <MDBCol md='6' xs='12'>
                          <h3 className='fs-5'>{t('uploads.details.disc_price').toUpperCase()}:</h3>
                          <li ><b> {process.env.REACT_APP_CUR} {h.discPrice} </b></li>
                          </MDBCol> 
                        
                                                                    
                      </MDBRow>
                      <MDBCol md='6' xs='12' className='mt-2 mx-auto'>
                          <h3 className='fs-5'>{t('uploads.details.ln').toUpperCase()}:</h3>
                          <li ><b> {process.env.REACT_APP_CUR} {h.landlord} </b></li>
                          </MDBCol> 
                      <MDBRow className='mt-2'>
                      {facilities.length>0 && facilities[0].length>1 &&(
                        <MDBCol md={6} >
                       
                        <MDBCard>
                            <Card.Header className='fw-bold text-capitalize'>
                               {t('house dtails.facilities')}
                            </Card.Header>
                            <Card.Body>
                                {facilities.map((f,i)=>{
                                    return(
                                        
                                        <ListGroup.Item key={i}>{t('choose.facilities.'+f)}</ListGroup.Item>
                                          
                                    )
                                })}
                               
                            </Card.Body>
                        </MDBCard>
                        
                        </MDBCol>
                        
                    )}
                    {rules.length>0 && rules[0].length>1 &&(
                            <MDBCol className='mt-2 mx-auto' >
                            <Card className='mx-auto'>
                                {/*<Card.Header className='fw-bold'>
                               {t('house dtails.not allowed')}
                                    
                                </Card.Header>*/}
                                <Card.Body>
                                    <ListGroup>

                                        { rules.map((rule,i)=>{                                            
                                            return(
                                                <ListGroup.Item key={i}>{t('uploads.rules.'+rule)}{' '}<FaCheck/></ListGroup.Item>
                                            )
                                        })}
                                    </ListGroup>                                                                        
                                </Card.Body>
                            </Card>                    
                        </MDBCol>
                        )}       
                      </MDBRow>
                     
                      
                      </MDBModalBody>
                      <MDBModalFooter>
                      <MDBBtn color="secondary" onClick={closeModal}>{t('close')}</MDBBtn>
                      
                      </MDBModalFooter>
                  </MDBModal>
                  </>     
    )
}
