import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react'
import { Button, Col, Form } from 'react-bootstrap'
import { useTranslation } from 'react-i18next';
import { Context } from '../../Context';
import { handleNwErr } from '../../utils/handleNetworkErr';

export default function Address({currentStep,next,prev,handleChange,lastStep,state}) {
    const [cities,setCities]=useState([])
    const {t}=useTranslation()
    const [loading,setLoading]=useState(false)
    const {dispErr}=useContext(Context)
    useEffect(()=>{
        setLoading(true)
        axios.get(process.env.REACT_APP_ADDRESS+'/house/city',{withCredentials:true})
        .then(resp=>{
            
            setCities(resp.data.map(city=>{
                return city.name
            }))
        })
        .catch(e=>{
            console.log(e);
            handleNwErr(e,dispErr)
        })
        .finally(()=>setLoading(false))
     
   
    },[])
    if(currentStep!==2){
        return null;
    }
  return (
    <div id='address' className='py-4 mh-100 w-100'>
    <Form onSubmit={next} className='d-block mx-auto'>
        <h4 className='text-center'>{t("choose.location")} </h4>
        <Col md={8} lg={5} className='mx-auto card p-2 border-0 '>
        <Form.Group className='' >
            <Form.Label>{t("choose.ct")}</Form.Label>
            <Form.Select required name='city' onChange={handleChange}  >
                <option value={''}></option>
                {cities.map((city,i)=>{
                    return(
                        <option key={i} value={city} selected={state.city===city}>{city}</option>
                    )
                })}
            </Form.Select>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.address")}:</Form.Label>
            <Form.Control type='text' required name='location' onChange={handleChange} defaultValue={state.location}></Form.Control>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.bedrooms")}:</Form.Label>
            <Form.Control type='number' required name='rooms' onChange={handleChange} defaultValue={state.rooms}></Form.Control>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.livrooms")}:</Form.Label>
            <Form.Control type='number' required name='livrooms' onChange={handleChange} defaultValue={state.livrooms}></Form.Control>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.toilets")}:</Form.Label>
            <Form.Control type='number' min={0} required name='toilets' onChange={handleChange} defaultValue={state.toilets}></Form.Control>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.balconies")}:</Form.Label>
            <Form.Control type='number' required name='balconies' onChange={handleChange} defaultValue={state.balconies}></Form.Control>
        </Form.Group>
        <Form.Group>
            <Form.Label>{t("choose.guests")}:</Form.Label>
            <Form.Control type='number' required name='guestnum' onChange={handleChange} defaultValue={state.guestnum}></Form.Control>
        </Form.Group>
        </Col>
       <div className='text-center'>
            <Button onClick={prev} disabled={currentStep<=1?true:false}>{t("choose.prev")}</Button>
            <Button  disabled={currentStep>=lastStep?true:false} type='submit'>{t("choose.next")}</Button>
        </div>
        </Form>
    </div>
  )
}
