import React, { useContext } from 'react'
import { Button } from 'react-bootstrap'
import { useTranslation } from 'react-i18next'
import { Link } from 'react-router-dom'
import { Context } from '../../Context'

export default function Success({currentStep}) {
  const {isAdmin}=useContext(Context)
   const {t}=useTranslation()
    if(currentStep!==6){
        return null
    }
  return (
    <div style={{position:'relative',top:'50px'}}>
        <h2 className='text-center'>{t('choose.successful.heading')}! </h2>
        <div>
        <p className='text-center'>{t('choose.successful.p')}</p>
            <Button onClick={()=>window.location.reload(false)} >{t('choose.successful.op_1')}</Button>
            {
              isAdmin===1?(
                <Link to='/admin' className='btn'>{t('choose.successful.op_2')}</Link>
              ):(
                <Link to='/user' className='btn'>{t('choose.successful.op_2')}</Link>
              )
            }
           
        </div>
    </div>
  )
}
