import React, { useContext } from 'react';
import {
  CDBSidebar,
  CDBSidebarContent,
  CDBSidebarHeader,
  CDBSidebarMenu,
  CDBSidebarMenuItem,
  CDBSidebarFooter
  
} from 'cdbreact';
import { Link } from 'react-router-dom';
import { NavDropdown } from 'react-bootstrap';
import { Context } from '../Context';
import { useTranslation } from 'react-i18next';

const AdminSidebar = () => {
  const {logout}=useContext(Context)
  return (
    <div >
      <CDBSidebar style={{}}>
        <CDBSidebarHeader prefix={<i className="fa fa-bars" />}>Hello, Admin</CDBSidebarHeader>
        <CDBSidebarContent>
          <CDBSidebarMenu>
            <Link to='/admin'><CDBSidebarMenuItem >Houses</CDBSidebarMenuItem></Link>
            <Link to='/admin/upload'><CDBSidebarMenuItem  > Upload</CDBSidebarMenuItem></Link>
            <Link to='/admin/all-users'><CDBSidebarMenuItem >Users</CDBSidebarMenuItem></Link>
            <Link to='/admin/bookings'><CDBSidebarMenuItem  > Reservations</CDBSidebarMenuItem></Link>
            <Link to='/admin/reviews'><CDBSidebarMenuItem  > Reviews</CDBSidebarMenuItem></Link>
            <Link to='/admin/all-posts'><CDBSidebarMenuItem  > Blog</CDBSidebarMenuItem></Link>
            <Link to='/admin/settings'><CDBSidebarMenuItem  > Settings</CDBSidebarMenuItem></Link>
          </CDBSidebarMenu>
        </CDBSidebarContent>

        <CDBSidebarFooter style={{ textAlign: 'center' }}>
          <div
            className="sidebar-btn-wrapper"
            style={{padding: '20px 5px'}}
            onClick={logout}
          >
            <CDBSidebarMenuItem  onClick={logout}>Logout</CDBSidebarMenuItem>
            
          </div>
        </CDBSidebarFooter>
      </CDBSidebar>
      </div>
  );
};

const UserSidebar = () => {
  const {logout}=useContext(Context)
  const {t}=useTranslation()
  return (
      <CDBSidebar className=' text-capitalize text-dark bg-light' >
        <CDBSidebarHeader prefix={<i className="fa fa-bars" />}>Side Menu</CDBSidebarHeader>
        <CDBSidebarContent >
          <CDBSidebarMenu className="text-dark">
            <CDBSidebarMenuItem ><Link to="/user/" className="text-dark">{t('myUploads')}</Link></CDBSidebarMenuItem>
            <CDBSidebarMenuItem ><Link to="/user/upload" className="text-dark">{t('upload')}</Link></CDBSidebarMenuItem>
            <CDBSidebarMenuItem  > <Link to="/user/settings" className="text-dark">{t('profile')}</Link></CDBSidebarMenuItem>
            {/*<CDBSidebarMenuItem  > <Link to="/user/settings" ></Link></CDBSidebarMenuItem>*/}
          </CDBSidebarMenu>
        </CDBSidebarContent>

        <CDBSidebarFooter >
          <div
            className="sidebar-btn-wrapper"
            style={{padding: '20px 5px'}}
          >
            
            <CDBSidebarMenuItem  onClick={logout}>{t('log out')}</CDBSidebarMenuItem>
          </div>
        </CDBSidebarFooter>
      </CDBSidebar>
  );
};

export  {AdminSidebar,UserSidebar};