import React, { useContext } from 'react'
import { Alert, Button, Modal } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import { Context } from '../Context'

export default function ErrorAlert() {
    const {showErr,hideErr}=useContext(Context)
    const {t}=useTranslation()
    if(showErr===false)return null;
   
        return (
            
            <Modal  show={showErr} onHide={hideErr} centered>
                <Modal.Header className='text-center text-danger fw-bold fs-3' closeButton>{t('err.net_err')} </Modal.Header>
                <Modal.Body>
                
                <p className='fw-bold'>{t('err.what_should_i_do')}</p>
                    <ul >
                        <li>{t('err.do_1')}</li>
                        <li>{t('err.do_2')}</li>
                    </ul>
                </Modal.Body>
                

                
          <Modal.Footer>
          <Button onClick={() => window.location.reload()} variant='warning'>
          {t('err.refresh')} 
          </Button>
          <Button variant='secondary' onClick={hideErr}> {t('err.dismiss')} </Button>
          </Modal.Footer>
                </Modal>
                
                
        )
        
      
  
}
