import React, { useContext, useState } from 'react';
import Alert from 'react-bootstrap/Alert';
//import Button from 'react-bootstrap/Button';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { Context } from '../Context';

export default function SetProfileAlert() {
  const {warn, hideWarn,user} = useContext(Context)
    const {t}=useTranslation()
  if (warn===true) {
    return (
      <Alert variant="warning" onClose={hideWarn} dismissible >
        <Alert.Heading className='text-md-center'>{t('set_profile_warn.title')}</Alert.Heading>
        <p className='text-md-center'>
        {t('set_profile_warn.desc')}
        </p>
        
        <Link to={parseInt(user.isAdmin)===1?'/admin/settings':'/user/settings'} 
        onClick={hideWarn}
        className='btn border border-primary d-block mx-auto'>
        {t('set_profile_warn.go_edit')}</Link>
        </Alert>
    );
  }
  return null;
}

